import React, { useEffect, useState } from 'react';
import axios from 'axios';
import UserEditModal from '../UserEditModal';
import './Profile.css';

interface User {
    username: string;
    name: string;
    role: string;
    email: string;
    company: string;
    password?: string;
}

const Profile: React.FC = () => {
    const [user, setUser] = useState<User | null>(null);
    const [isEditing, setIsEditing] = useState<boolean>(true); // Default to editing mode

    useEffect(() => {
        const fetchUser = async () => {
            const storedUsername = localStorage.getItem('username');
            if (!storedUsername) {
                console.error('Username not found in local storage');
                return;
            }

            try {
                const response = await axios.get<User>(`/api/users/${storedUsername}`);
                setUser(response.data);
            } catch (error) {
                console.error('Error fetching user:', error);
            }
        };

        fetchUser();
    }, []);

    const handleSave = async (updatedUser: User) => {
        if (!user) return;

        const changedFields: Partial<User> = {};
        for (const key in updatedUser) {
            if (updatedUser[key as keyof User] !== user[key as keyof User]) {
                changedFields[key as keyof User] = updatedUser[key as keyof User];
            }
        }

        try {
            await axios.put(`/api/users/${updatedUser.username}`, changedFields);
            setUser(updatedUser);
            setIsEditing(false);
        } catch (error) {
            console.error('Error saving user:', error);
        } finally {
            window.location.href = '/deskflow';
        }
    };

    const handleClose = () => {
        setIsEditing(false);
        window.location.href = '/deskflow';
    };

    if (!user) {
        return <div>Loading...</div>;
    }

    return (
        <div className="profile-container">
            {isEditing && (
                <UserEditModal
                    user={user}
                    onSave={handleSave}
                    onClose={handleClose}
                    option='Alterando'
                />
            )}
        </div>
    );
};

export default Profile;
