import React, { useEffect, useState, useCallback } from 'react';
import ConfirmationModal from "../ConfirmationModal/ConfirmationModal";
import axios from 'axios';
import './Services.css';
import Log from '../Log/Log';
import Environments from '../Environments/Environments'; // Import the PatchApplyForm component
import DownloadFile from '../DownloadFile/DownloadFile'; // Import the PatchApplyForm component

export interface Service {
  Name: string;
  DisplayName: string;
  Status: string;
}

export interface logParams {
  serviceName: string;
  action: string;
  origin?: string;
  destination?: string;
  origin_tenant?: string;
  destination_tenant?: string;
  log_result?: string;
}

export interface ServicesProps {
  selectedTenantId: string | null;
  selectedTenantName: string | null;
}

export interface PatchApplyFormProps {
  tenants: any[];
  onClose: () => void;

}

const Services: React.FC<ServicesProps> = ({ selectedTenantId, selectedTenantName }) => {
  const [services, setServices] = useState<Service[]>([]);
  const [tenants, setTenants] = useState<any[]>([]); // Initialize as an empty array
  const [logMessages, setLogMessages] = useState<string[]>([]);
  const [showLog, setShowLog] = useState<boolean>(false);
  const [isLoadingService, setIsLoadingService] = useState<string | null>(null);
  const [showBalloon, setShowBalloon] = useState<boolean>(false);
  const [showWait, setShowWait] = useState<boolean>(false);
  const [originEnvironments, setOriginEnvironments] = useState<string[]>([]);
  const [destinationEnvironments, setDestinationEnvironments] = useState<string[]>([]);
  const [selectedService, setSelectedService] = useState<string | null>(null);
  const [selectedActions, setSelectedActions] = useState<{ [key: string]: string }>({});
  const [showDownloadFile, setShowDownloadFile] = useState<boolean>(false); // For DownloadFile
  const [downloadFileServiceName, setDownloadFileServiceName] = useState<string | null>(null);
  const [downloadFileType, setDownloadFileType] = useState<'ini' | 'log' | null>(null);

  const [showFileModal, setShowFileModal] = useState<boolean>(false);
  const [fileContent, setFileContent] = useState<string | null>(null);
  const [currentFileName, setCurrentFileName] = useState<string>('');
  const [fileType, setFileType] = useState<string>('text');
  const [fileBlob, setFileBlob] = useState<Blob | null>(null);
  const [isLoading, setIsLoading] = useState<boolean>(false);

  const [isModalOpen, setIsModalOpen] = useState(false);
  const [modalAction, setModalAction] = useState<() => void>(() => { });
  const [allowExclusiveMode, setExclusiveMode] = useState(true);
  const [servicePorts, setServicePorts] = useState<{
    [key: string]: {
      tcp: string;
      webagent: string;
      dbaccess: string;
      http: string;
      webapp: string;
      httprest: string;
    }
  }>({});
  const [ShowPorts, setShowPorts] = useState<boolean>(false);

  const [activeTab, setActiveTab] = useState<'Serviços' | 'Ambientes' | 'Arquivos'>('Serviços'); // Add 'Arquivos' tab
  const [fileList, setFileList] = useState<string[]>([]); // Add state to store file list

  const [userGroup, setUserGroup] = useState<string | null>(null);
  const [isAllowed, setIsAllowed] = useState<boolean>(false);
  const [topologyPermissions, setTopologyPermissions] = useState<{ allow01: number } | null>(null);

  // Fetch user group and check permissions
  useEffect(() => {
    const fetchUserGroup = async () => {
      const username = localStorage.getItem('username');
      if (!username) return;

      try {
        const response = await axios.get(`/api/users/${username}`);
        const group = response.data.role;
        setUserGroup(group);

        // Allow all accesses if the group is 'admin'
        if (group === 'admin') {
          setIsAllowed(true);
          return;
        }

        // Fetch topology permissions for the user's group
        if (selectedTenantId) {
          const permissionsResponse = await axios.get(`/api/topology_access/${selectedTenantId}/${group}`);
          const permissionsData = permissionsResponse.data[0]; // Access the first object in the array
          if (permissionsData) {
            setTopologyPermissions(permissionsData);
            setIsAllowed(permissionsData.allow01 === 1); // Check if allow01 is enabled
          } else {
            setTopologyPermissions(null);
            setIsAllowed(false); // Set not allowed if no data is returned
          }
        }
      } catch (error) {
        console.error('Error fetching user group or permissions:', error);
      }
    };

    fetchUserGroup();
  }, [selectedTenantId]);

  const fetchServicePorts = async (serviceName: string) => {
    try {
      const response = await axios.get(`/api/get-ports/${serviceName}`);
      if (response.data.success && response.data.data) {
        return {
          serviceName,
          ports: {
            tcp: response.data.data.tcp || '-',
            webagent: response.data.data.webagent || '-',
            dbaccess: response.data.data.dbaccess || '-',
            http: response.data.data.http || '-',
            webapp: response.data.data.webapp || '-',
            httprest: response.data.data.httprest || '-'
          }
        };
      }
    } catch (error) {
      console.error(`Error fetching port for ${serviceName}:`, error);
    }
    return {
      serviceName,
      ports: {
        tcp: '-',
        webagent: '-',
        dbaccess: '-',
        http: '-',
        webapp: '-',
        httprest: '-'
      }
    };
  };

  const handleDownloadFile = async (serviceName: string, fileType: 'ini' | 'log') => {
    if (!selectedTenantId) return;
    try {
      const endpoint =
        fileType === 'ini'
          ? `${getBaseUrl()}/api/get-ini/${serviceName}`
          : `${getBaseUrl()}/api/get-log/${serviceName}`;

      const response = await axios.get(endpoint, { responseType: 'blob' }); // Ensure the response is treated as a file blob

      const url = window.URL.createObjectURL(new Blob([response.data]));
      const link = document.createElement('a');
      link.href = url;
      link.setAttribute('download', `${serviceName}.${fileType}`); // Set the file name
      document.body.appendChild(link);
      link.click();
      document.body.removeChild(link);
    } catch (error) {
      console.error(`Error downloading ${fileType} file for ${serviceName}:`, error);
    }
  };

  const viewFileContent = async (serviceName: string, fileType: 'ini' | 'log') => {
    if (!selectedTenantId) return;
    setIsLoading(true);
    try {
      const endpoint =
        fileType === 'ini'
          ? `${getBaseUrl()}/api/get-ini/${serviceName}`
          : `${getBaseUrl()}/api/get-log/${serviceName}`;

      const response = await axios.get(endpoint, { responseType: 'blob' }); // Fetch as blob
      const fileBlob = new Blob([response.data], { type: 'text/plain' });
      const fileText = await fileBlob.text(); // Convert blob to text
      setFileContent(fileText); // Set file content for modal
      setCurrentFileName(`${serviceName}.${fileType}`);
      setFileType(fileType);
      setShowFileModal(true);
    } catch (error) {
      console.error(`Error viewing ${fileType} file for ${serviceName}:`, error);
    } finally {
      setIsLoading(false);
    }
  };

  const openModal = (action: () => void) => {
    setModalAction(() => action);
    setIsModalOpen(true);
  };

  const confirmAction = () => {
    if (modalAction) modalAction();
    setIsModalOpen(false);
  };

  const cancelAction = () => {
    setIsModalOpen(false);
  };

  const getBaseUrl = () => {
    return ''; // Adjust this according to your backend server setup
  };
  // Fetch services for the selected tenant
  const fetchServices = useCallback(async () => {
    if (!selectedTenantId) return;

    //alert(`vai atualizar ${ShowPorts} `);

    try {

      const response = await axios.get<Service[]>(`/api/get-services/${selectedTenantId}`);
      const servicesData = response.data;

      // Fetch ports for all services
      if (ShowPorts) {
        const portsPromises = servicesData.map((service) => fetchServicePorts(service.Name));
        const portsResults = await Promise.all(portsPromises);

        // Create a mapping of service names to port objects
        const portsMap = portsResults.reduce((acc, result) => {
          if (result) {
            acc[result.serviceName] = result.ports;
          }
          return acc;
        }, {} as { [key: string]: { tcp: string; webagent: string; dbaccess: string; http: string; webapp: string; httprest: string } });

        setServicePorts(portsMap);
      }
      setServices(servicesData);
      fetchExclusiveMode();
    } catch (error) {
      console.error('Error fetching services:', error);
    }
  }, [selectedTenantId, ShowPorts]);

  useEffect(() => {
    const checkFiles = async () => {
      if (!selectedTenantId) return;
      try {
        const response = await axios.get(`${getBaseUrl()}/api/list-files/${selectedTenantId}`);
        if (response.data.length === 0) {
          document.querySelector('.action-buttons-artefatos')?.classList.add('hidden');
        } else {
          document.querySelector('.action-buttons-artefatos')?.classList.remove('hidden');
        }
      } catch (error) {
        console.error('Error checking files:', error);
      }
    };

    checkFiles();
  }, [selectedTenantId]);

  const fetchExclusiveMode = async () => {
    try {
      const response = await axios.get(`/api/exclusive-mode/${selectedTenantId}`);
      const exclusiveMode = response.data.exclusive_mode;
      setExclusiveMode(!exclusiveMode);
    } catch (error) {
      console.error('Error fetching exclusive mode:', error);
    }
  };

  useEffect(() => {
    const intervalId = setInterval(fetchServices, 10000); // Schedule fetchServices to run every 15 seconds
    return () => clearInterval(intervalId); // Cleanup interval on component unmount
  }, [fetchServices]);

  // Fetch tenants
  const fetchTenants = useCallback(async () => {
    try {
      const response = await axios.get(`${getBaseUrl()}/api/get-tenants`);
      // console.log('Fetched tenants:', response.data);
      if (Array.isArray(response.data)) {
        // Response is an array
        setTenants(response.data);
      } else if (Array.isArray(response.data.tenants)) {
        // Response is an object with a 'tenants' array
        setTenants(response.data.tenants);
      } else {
        console.error('Unexpected response format:', response.data);
        setTenants([]);
      }
    } catch (error) {
      console.error('Error fetching tenants:', error);
    }
  }, []);

  useEffect(() => {
    fetchServices();
    fetchTenants();
  }, [fetchServices, fetchTenants]);

  const addLogMessage = (message: string) => {
    setLogMessages((prevMessages) => [...prevMessages, message]);
  };

  // Function to create a new log entry
  const createLogEntry = async (params: logParams) => {
    try {
      const originTenantName = tenants.find(tenant => tenant.id === params.origin_tenant)?.name || '';
      const destinationTenantName = tenants.find(tenant => tenant.id === params.destination_tenant)?.name || '';

      const now = new Date();
      const logEntry = {
        date: now.toISOString().split('T')[0],
        time: now.toTimeString().split(' ')[0],
        user: localStorage.getItem('username') || 'unknown', // Assuming you store username in localStorage
        tenant: selectedTenantName || 'unknown',
        service: params.serviceName,
        action: params.action,
        origin: params.origin || '',
        origin_tenant: originTenantName || '',
        destination_tenant: destinationTenantName || '',
        destination: params.destination || '',
        result: 'In Progress',
        log_result: params.log_result
      };
      const response = await axios.post(`${getBaseUrl()}/api/logs`, logEntry);
      return response.data.id; // Assuming the response includes the log ID
    } catch (error) {
      console.error('Error creating log entry:', error);
      return null;
    }
  };

  // Function to update the log entry with the finish time and result
  const updateLogEntry = async (logId: number, result: string, full_log?: string) => {
    try {
      const now = new Date();
      await axios.put(`${getBaseUrl()}/api/logs/${logId}`, {
        finish_date: now.toISOString().split('T')[0],
        finish_time: now.toLocaleTimeString('en-US', { hour12: false }),
        result: result,
        log_result: full_log
      });
    } catch (error) {
      console.error('Error updating log entry:', error);
    }
  };

  const executeServiceAction = async (serviceName: string, action: 'start' | 'stop' | 'restart') => {
    setIsLoadingService(serviceName);
    setSelectedService(null);

    setSelectedActions((prevActions) => ({
      ...prevActions,
      [serviceName]: '', // Reset the selected action to default
    }));

    setServices((prevServices) =>
      prevServices.map((service) =>
        service.Name === serviceName ? { ...service, Status: 'Updating' } : service
      )
    );

    const logId = await createLogEntry({ serviceName: serviceName, action: `${action} service` } as logParams);

    try {
      await axios.post(`${getBaseUrl()}/api/${action}-service`, { serviceName });
      addLogMessage(`Service ${serviceName} ${action}ed successfully.`);
      fetchServices();
      setShowBalloon(true); // Show green balloon
      setTimeout(() => setShowBalloon(false), 3000); // Hide after 3 seconds
      if (logId) await updateLogEntry(logId, 'Success');
    } catch (error) {
      const errorMessage = error instanceof Error ? error.message : String(error);
      addLogMessage(`Error ${action}ing service ${serviceName}: ${errorMessage}`);
      console.error(`Error ${action}ing service:`, error);
      if (logId) await updateLogEntry(logId, 'Failed', errorMessage);
    } finally {
      setIsLoadingService(null);
    }
  };

  const handleExclusiveAction = async (action: 'on' | 'off') => {
    const logId = await createLogEntry({ serviceName: 'Exclusive Mode', action: `Set exclusive mode ${action}` } as logParams);
    setServices((prevServices) =>
      prevServices.map((service) => ({ ...service, Status: 'Updating' }))
    );

    try {
      await axios.post(`${getBaseUrl()}/api/exclusive-mode-${action}/${selectedTenantId}`);
      addLogMessage(`Exclusive Mode Activated successfully.`);
      fetchServices();
      setShowBalloon(true); // Show green balloon
      setTimeout(() => setShowBalloon(false), 3000); // Hide after 3 seconds
      if (logId) await updateLogEntry(logId, 'Success');
      setExclusiveMode(action === "on" ? false : true)
    } catch (error) {
      const errorMessage = error instanceof Error ? error.message : String(error);
      console.error(`Error in exclusive mode ${action}:`, error);
      if (logId) await updateLogEntry(logId, 'Failed', errorMessage);
    }
  };

  const handleBulkAction = async (action: 'start-all' | 'stop-all' | 'restart-all') => {
    const logId = await createLogEntry({ serviceName: 'Bulk Action', action: action.replace('-', ' ') } as logParams);
    setServices((prevServices) =>
      prevServices.map((service) => ({ ...service, Status: 'Updating' }))
    );

    try {
      await axios.post(`${getBaseUrl()}/api/${action}/${selectedTenantId}`);
      addLogMessage(`${action.replace('-', ' ')} executed successfully.`);
      fetchServices(); // Refresh the services after the action is completed
      setShowBalloon(true); // Show green balloon
      setTimeout(() => setShowBalloon(false), 3000); // Hide after 3 seconds
      if (logId) await updateLogEntry(logId, 'Success');
    } catch (error) {
      const errorMessage = error instanceof Error ? error.message : String(error);
      console.error(`Error in bulk action: ${action}`, error);
      addLogMessage(`Error executing ${action}: ${error}`);
      if (logId) await updateLogEntry(logId, 'Failed', errorMessage);
    }
  };

  const getStatusColor = (status: string) => {
    if (status === 'Running') return 'green';
    if (status === 'Stopped') return 'red';
    if (status === 'Updating') return 'orange';
    return 'grey';
  };

  const closeFileModal = () => {
    setShowFileModal(false);
    setFileContent(null); // Clear file content from memory
    setFileBlob(null); // Clear file blob from memory
    setCurrentFileName(''); // Reset file name
    setFileType('text'); // Reset file type
  };

  const handleDownload = () => {
    if (!fileContent || !currentFileName) return;
    const blob = new Blob([fileContent], { type: 'text/plain' });
    const url = window.URL.createObjectURL(blob);
    const link = document.createElement('a');
    link.href = url;
    link.setAttribute('download', currentFileName);
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
  };

  useEffect(() => {
    const checkFiles = async () => {
      if (!selectedTenantId) return;
      try {
        const response = await axios.get(`${getBaseUrl()}/api/list-files/${selectedTenantId}`);
        if (response.data.length === 0) {
          document.querySelector('.action-buttons-artefatos')?.classList.add('hidden');
        } else {
          document.querySelector('.action-buttons-artefatos')?.classList.remove('hidden');
        }
      } catch (error) {
        console.error('Error checking files:', error);
      }
    };

    if (activeTab === 'Arquivos') {
      checkFiles(); // Ensure files are listed when switching to the "Arquivos" tab
    }
  }, [selectedTenantId, activeTab]); // Trigger when the active tab changes

  const fetchFiles = async () => {
    if (!selectedTenantId) return;
    try {
      const response = await axios.get(`${getBaseUrl()}/api/list-files/${selectedTenantId}`);
      if (Array.isArray(response.data)) {
        // Map the response to extract the 'name' property if the items are objects
        const files = response.data
          .map((file: string | { id: string; name: string }) =>
            typeof file === 'string' ? file : file.name
          ); // Ensure only strings are included
        setFileList(files); // Update file list with extracted names
      } else {
        console.error('Unexpected response format:', response.data);
      }
    } catch (error) {
      console.error('Error fetching files:', error);
    }
  };

  useEffect(() => {
    if (activeTab === 'Arquivos') {
      setFileList([]); // Clear the file list to prevent duplication
      fetchFiles(); // Fetch files when switching to the "Arquivos" tab
    }
  }, [selectedTenantId, activeTab]); // Trigger when the active tab changes

  return (
    <div className="services">
      <h2>{selectedTenantId ? `${selectedTenantName} ` : ''}</h2>
      {showBalloon && <div className="success-balloon">Ação executada com sucesso!</div>}
      {showWait && <div className="success-balloon">Aguarde...</div>}
      {isLoading && <div className="success-balloon">Carregando o arquivo. Aguarde...</div>}
      <div className="tabs" style={{ display: 'flex', justifyContent: 'flex-start', gap: '10px', width: 'auto' }}>
        <button
          className={activeTab === 'Serviços' ? 'active-tab' : ''}
          onClick={() => setActiveTab('Serviços')}
          style={{ flex: 'none', padding: '10px 20px' }}
        >
          Serviços
        </button>
        <button
          className={activeTab === 'Ambientes' ? 'active-tab' : ''}
          onClick={() => setActiveTab('Ambientes')}
          style={{ flex: 'none', padding: '10px 20px' }}
        >
          Ambientes
        </button>
        <button
          className={activeTab === 'Arquivos' ? 'active-tab' : ''}
          onClick={() => setActiveTab('Arquivos')}
          style={{ flex: 'none', padding: '10px 20px' }}
        >
          Arquivos
        </button>
      </div>

      {activeTab === 'Serviços' && (
        <div className="content-wrapper">
          <div className="action-buttons-container">
            <div className="action-buttons">
              <button
                onClick={() => openModal(() => handleExclusiveAction(allowExclusiveMode ? "on" : "off"))}
                disabled={!isAllowed}
              >
                {allowExclusiveMode ? "Ativar Exclusivo" : "Desativar Exclusivo"}
              </button>
              <button onClick={() => openModal(() => handleBulkAction('start-all'))} disabled={!isAllowed}>
                Iniciar Todos
              </button>
              <button onClick={() => openModal(() => handleBulkAction('stop-all'))} disabled={!isAllowed}>
                Parar Todos
              </button>
              <button onClick={() => openModal(() => handleBulkAction('restart-all'))} disabled={!isAllowed}>
                Reiniciar
              </button>
            </div>
          </div>
          <label className="ports-checkbox">
            <input
              type="checkbox"
              checked={ShowPorts}
              onChange={(e) => { setShowPorts(e.target.checked); fetchServices(); }}
            />
            Mostrar Portas
          </label>
          <table className="services-table">
            <thead>
              <tr>
                <th>&nbsp;ID</th>
                <th>Status</th>
                <th>Nome</th>
                <th>Descrição</th>
                <th>Portas</th>
                <th>Ações</th>
              </tr>
            </thead>
            <tbody>
              {services.map((service, index) => (
                <tr key={service.Name} className={index % 2 === 0 ? 'even-row' : 'odd-row'}>
                  <td>{index + 1}</td>
                  <td>
                    <span
                      className="status-indicator"
                      style={{ backgroundColor: getStatusColor(service.Status) }}
                    />
                    <span>{service.Status}</span>
                    <select
                      value={selectedActions[service.Name] || ''}
                      onChange={(e) => {
                        const action = e.target.value as 'start' | 'stop' | 'restart';
                        setSelectedActions((prevActions) => ({
                          ...prevActions,
                          [service.Name]: action,
                        }));
                        executeServiceAction(service.Name, action);
                      }}
                      disabled={!isAllowed}
                    >
                      <option value="" disabled>Ações</option>
                      <option value="restart">Reiniciar Serviço</option>
                      <option value="start">Iniciar Serviço</option>
                      <option value="stop">Parar Serviço</option>
                    </select>
                  </td>
                  <td>{service.Name}</td>
                  <td>{service.DisplayName || '-'}</td>
                  <td>
                    {ShowPorts ? (
                      <select
                        className="ports-dropdown"
                        defaultValue="tcp"
                        onChange={(e) => {
                          // This is just to handle the dropdown selection, no action needed
                        }}
                      >
                        {Object.entries(servicePorts[service.Name] || {})
                          .filter(([_, value]) => value && value !== '-') // Only include options with valid content
                          .map(([key, value]) => (
                            <option key={key} value={key}>
                              {key.charAt(0).toUpperCase() + key.slice(1)}: {value}
                            </option>
                          ))}
                        {!Object.values(servicePorts[service.Name] || {}).some(value => value && value !== '-') && (
                          <option value="none">Loading...</option>
                        )}
                      </select>) : (
                      '-'
                    )}
                  </td>
                  <td>
                    <select
                      onChange={async (e) => {
                        const selectedOption = e.target.value;
                        if (selectedOption === 'view-ini') {
                          viewFileContent(service.Name, 'ini');
                        } else if (selectedOption === 'view-log') {
                          viewFileContent(service.Name, 'log');
                        } else if (selectedOption === 'download-ini') {
                          handleDownloadFile(service.Name, 'ini');
                        } else if (selectedOption === 'download-log') {
                          handleDownloadFile(service.Name, 'log');
                        } else if (selectedOption === 'delete-log') {
                          openModal(async () => {
                            try {
                              await axios.delete(`${getBaseUrl()}/api/delete-log/${service.Name}`);
                              setShowBalloon(true);
                              setLogMessages((prev) => [...prev, `Log do serviço ${service.Name} apagado com sucesso.`]);
                              setTimeout(() => setShowBalloon(false), 3000);
                            } catch (error) {
                              setShowBalloon(true);
                              setLogMessages((prev) => [...prev, `Erro ao apagar log do serviço ${service.Name}.`]);
                              setTimeout(() => setShowBalloon(false), 3000);
                              console.error('Erro ao apagar log:', error);
                            }
                          });
                        }
                        e.target.value = ''; // Reset to default option
                      }}
                      defaultValue=""
                    >
                      <option value="" disabled>Selecione uma opção</option>
                      <option value="view-ini">Visualizar .ini</option>
                      <option value="download-ini">Download .ini</option>
                      <option value="view-log">Visualizar Log</option>
                      <option value="download-log">Download Log</option>
                      <option value="delete-log">Apagar log</option>
                    </select>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      )}

      {activeTab === 'Ambientes' && (
        <div className="Environments">
          <Environments tenants={tenants} onClose={() => setActiveTab('Serviços')} />
        </div>
      )}

      {activeTab === 'Arquivos' && (
        <div className="content-wrapper">
          <div className="action-buttons-container">
            {( // Ensure downloadFileType is not null
              <DownloadFile
                tenantId={selectedTenantId}
              />
            )}
          </div>
        </div>
      )}
      {showFileModal && (
        <div className="Services-overlayViewLog">
          <div className="Services-modal">
            <div className="Services-modalDialog">
              <div className="Services-modalHeader">
                <h4>{currentFileName}</h4>
                <div className="modalActions">
                  <button
                    className="button"
                    onClick={handleDownload}
                    disabled={isLoading}
                  >
                    Download
                  </button>
                  <button
                    className="button"
                    onClick={closeFileModal}
                  >
                    Fechar
                  </button>
                </div>
              </div>
              <div className="Services-modalBody">
                <pre className="Services-modalBody">{fileContent}</pre>
              </div>
            </div>
          </div>
        </div>
      )}
      <ConfirmationModal
        isOpen={isModalOpen}
        title="Confirmação Operação?"
        message=""
        onConfirm={confirmAction}
        onCancel={cancelAction}
      />
    </div>
  );
};

export default Services;