import React, { useState, useEffect, useCallback } from 'react';
import { useForm } from 'react-hook-form';
import axios from 'axios';
import styles from './PatchApplyForm.module.css'; // Import the CSS Module
import { logParams } from '../Services/Services';
import ConfirmationModal from '../ConfirmationModal/ConfirmationModal'; // Import the modal component
import { group } from 'console';

interface Tenant {
  id: string;
  name: string;
}

interface PatchApplyFormProps {
  tenants: Tenant[];
  onClose: () => void;
}

interface FormData {
  tenant: string;
  environment: string;
  patchFile: FileList;
}

const getBaseUrl = () => {
  return ''; // Replace with your actual base URL
};

const PatchApplyForm: React.FC<PatchApplyFormProps> = ({ tenants, onClose }) => {
  const { register, handleSubmit, reset, watch, formState: { errors }, resetField } = useForm<FormData>();
  const [uploadProgress, setUploadProgress] = useState<number | null>(null);
  const [isApplyingPatch, setIsApplyingPatch] = useState<boolean>(false);
  const [isRunning, setIsRunning] = useState<boolean>(false);
  const [progress, setProgress] = useState<number>(0); // Progress for patching process
  const [successMessage, setSuccessMessage] = useState<string | null>(null);
  const [errorMessage, setErrorMessage] = useState<string | null>(null);
  const [logContent, setLogContent] = useState<string | null>(null);
  const [tenantEnvironments, setTenantEnvironments] = useState<string[]>([]);
  const [filePath, setFilePath] = useState<string | null>(null);
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [modalMessage, setModalMessage] = useState<string | null>(null);
  const selectedTenant = watch('tenant');
  const [userGroup, setUserGroup] = useState<string | null>(null);

  const createLogEntry = async (params: logParams) => {
    try {
      const originTenantName = tenants.find(tenant => tenant.id === params.origin_tenant)?.name || '';
      const destinationTenantName = tenants.find(tenant => tenant.id === params.destination_tenant)?.name || '';
      const now = new Date();
      const logEntry = {
        date: now.toISOString().split('T')[0],
        time: now.toTimeString().split(' ')[0],
        user: localStorage.getItem('username') || 'unknown',
        tenant: originTenantName,
        service: params.serviceName,
        action: params.action,
        origin: '',
        origin_tenant: filePath,
        destination_tenant: destinationTenantName || '',
        destination: params.destination || '',
        result: 'In Progress',
        log_result: params.log_result
      };
      const response = await axios.post(`${getBaseUrl()}/api/logs`, logEntry);
      return response.data.id;
    } catch (error) {
      console.error('Error creating log entry:', error);
      return null;
    }
  };

  const updateLogEntry = async (logId: number, result: string, full_log?: string) => {
    try {
      const now = new Date();
      await axios.put(`${getBaseUrl()}/api/logs/${logId}`, {
        finish_date: now.toISOString().split('T')[0],
        finish_time: now.toLocaleTimeString('en-US', { hour12: false }),
        result: result,
        log_result: full_log
      });
    } catch (error) {
      console.error('Error updating log entry:', error);
    }
  };

  const handleFileChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    const files = event.target.files;
    if (files && files.length > 0) {
      setFilePath(Array.from(files).map(file => file.name).join(', '));
    }
  };

  const fetchEnvironmentsForTenant = useCallback(
    async (tenantId: string) => {
      if (!tenantId) return;
      try {
        const response = await axios.get<{ environments: string[] }>(`${getBaseUrl()}/api/get-environments/${tenantId}`);
        setTenantEnvironments(response.data.environments || []);
      } catch (error) {
        console.error('Error fetching environments:', error);
      }
    },
    []
  );

  useEffect(() => {
    if (selectedTenant) {
      fetchEnvironmentsForTenant(selectedTenant);
      resetField('environment'); // Reset the destination environment
    }
  }, [selectedTenant, fetchEnvironmentsForTenant, resetField]);

  // Patch applying progress
  useEffect(() => {
    if (isApplyingPatch) {
      const interval = setInterval(() => {
        setProgress((prev) => (prev < 100 ? prev + 10 : 100));
      }, 5000);
      return () => clearInterval(interval);
    }
  }, [isApplyingPatch]);

  useEffect(() => {
    const fetchUserGroup = async () => {
      const username = localStorage.getItem('username');
      if (!username) return;

      try {
        const response = await axios.get(`/api/users/${username}`);
        const group = response.data.role;
        setUserGroup(group);
      } catch (error) {
        console.error('Error fetching user group:', error);
      }
    };

    fetchUserGroup();
  }, []);

  const checkEnvironmentAccess = async (tenantId: string, groupId: string, environment: string): Promise<boolean> => {
    try {
      const response = await axios.get(`${getBaseUrl()}/api/environment_access/${tenantId}/${groupId}/${environment}`);
      const accessData = response.data.find((item: any) => item.allow02 === 1);
      return (!!accessData || groupId === 'admin');
    } catch (error) {
      console.error('Error checking environment access:', error);
      return false;
    }
  };

  const handleEnvironmentChange = async (environment: string) => {
    if (!selectedTenant || !userGroup) {
      setErrorMessage('Erro ao obter o grupo do usuário ou topologia selecionada.');
      return;
    }

    const hasAccess = await checkEnvironmentAccess(selectedTenant, userGroup, environment);

    if (!hasAccess) {
      setModalMessage('Você não tem permissão para aplicar o patch neste ambiente.');
      setIsModalOpen(true);
      resetField('environment'); // Reset the environment selection
    }
  };

  const onSubmit = async (data: FormData) => {
    setUploadProgress(null);
    setSuccessMessage(null);
    setErrorMessage(null);
    setIsApplyingPatch(false); // Reset patching progress

    if (!userGroup) {
      setErrorMessage('Erro ao obter o grupo do usuário.');
      return;
    }

    const logId = await createLogEntry({
      serviceName: 'Patch Apply',
      action: 'Apply',
      origin: data.environment,
      destination: data.environment,
      origin_tenant: data.tenant,
      destination_tenant: data.tenant,
    });

    try {
      const formData = new FormData();
      formData.append('tenant', data.tenant);
      formData.append('environment', data.environment);

      // Append up to 5 files
      Array.from(data.patchFile).slice(0, 5).forEach((file) => {
        formData.append('patchFiles', file);
      });

      setProgress(0);
      setLogContent(null);
      setIsRunning(true);

      // Upload progress bar
      const response = await axios.post('/api/apply-patch', formData, {
        onUploadProgress: (progressEvent) => {
          if (progressEvent.total) {
            const progress = Math.round((progressEvent.loaded * 100) / progressEvent.total);
            setUploadProgress(progress);
            if (progress >= 100) {
              setUploadProgress(100);
              setIsApplyingPatch(true); // Start patching progress
            }
          }
        },
      });

      setLogContent(response.data.log);
      if (logId) await updateLogEntry(logId, 'Success');
      setProgress(100);
      setSuccessMessage('Aplicado com sucesso!');
      reset();
    } catch (error) {
      if (logId) await updateLogEntry(logId, 'Failed', (error as any).response?.data?.error || (error as any).message);
      setIsApplyingPatch(false);
      setErrorMessage('Falha na aplicação do patch!');
    } finally {
      setIsRunning(false);
    }
  };

  const downloadLog = () => {
    if (logContent) {
      const blob = new Blob([logContent], { type: 'text/plain' });
      const link = document.createElement('a');
      link.href = window.URL.createObjectURL(blob);
      link.download = 'patch.log';
      link.click();
    }
  };

  return (
    <div className={styles.overlay}>
      <div className={styles.patchApplyForm}>
        <form onSubmit={handleSubmit(onSubmit)} className={styles.patchApplyForm}>
          {/* Form fields */}
          <div>
            <label><b>Topologia</b></label>
            <select {...register('tenant', { required: true })}>
              <option value="">Escolha a Topologia</option>
              {tenants.map((tenant) => (
                <option key={tenant.id} value={tenant.id}>
                  {tenant.name}
                </option>
              ))}
            </select>
            {errors.tenant && <span>This field is required</span>}
          </div>
          <div>
            <label><b>Environment</b></label>
            <select
              {...register('environment', { required: true })}
              onChange={(e) => handleEnvironmentChange(e.target.value)}
            >
              <option value="">Escolha o Environment</option>
              {tenantEnvironments.length > 0 ? (
                tenantEnvironments.map((environment, index) => (
                  <option key={index} value={environment}>
                    {environment}
                  </option>
                ))
              ) : (
                <option value="" disabled>No environments available</option>
              )}
            </select>
            {errors.environment && <span>This field is required</span>}
          </div>
          <div>
            <label><b>Arquivos de patch</b></label>
            <input type="file" {...register('patchFile', { required: true })} multiple onChange={handleFileChange} />
            {errors.patchFile && <span>This field is required</span>}
          </div>
          <div className={styles.buttonContainer}>
            <button type="submit" disabled={isRunning}>Aplicar Patch</button>
            <button type="button" disabled={isRunning} onClick={onClose}>Fechar</button>
          </div>
          {/* Upload Progress Bar */}
          {uploadProgress !== null && (
            <div>
              <div className={styles.uploadingContainer}>
                <div className={styles.uploadingProgress} style={{ width: `${uploadProgress}%` }}></div>
              </div>
              <div className={styles.textStatus}>Carregando...</div>
            </div>
          )}
          {/* Applying Patch Progress Bar */}
          {isApplyingPatch && (
            <div>
              <div className={styles.applyingContainer}>
                <div className={styles.applyingProgress} style={{ width: `${progress}%` }}></div>
              </div>
              <div className={styles.textStatus}>Aplicando patch...</div>
              {successMessage && <div className={styles.textStatus}>{successMessage}</div>}
            </div>
          )}

          {/* Success/Error Messages */}
          {errorMessage && <div className={styles.textStatus}>{errorMessage}</div>}

          {/* Download Log Button */}
          {logContent && (
            <div className={styles.logButtonContainer}>
              <button type="button" onClick={downloadLog}>Download Log</button>
            </div>
          )}
        </form>
        <ConfirmationModal
          isOpen={isModalOpen}
          title="Permissão Negada"
          message={modalMessage || ''}
          onConfirm={() => setIsModalOpen(false)}
          onCancel={() => setIsModalOpen(false)}
        />
      </div>
    </div>
  );
};

export default PatchApplyForm;
