import React, { useState, useEffect } from 'react';
import axios from 'axios';
import '../Services/Services.css'; // Use the same CSS as Services.css
import EnvironmentCRUD from '../EnvironmentCRUD/EnvironmentCRUD'; // Import the EnvironmentCRUD component
import { logParams } from '../Services/Services';
import ConfirmationModal from "../ConfirmationModal/ConfirmationModal";
import PatchApplyForm from '../PatchApplyForm/PatchApplyForm'; // Import the PatchApplyForm component
import styles from './Environments.module.css';

interface EnvironmentsProps {
  tenants: any[];
  onClose: () => void;
}

interface EnvironmentInfo {
  custom_hash: string;
  rpo_hash: string;
  custom_timestamp: string;
  rpo_timestamp: string;
}

const getBaseUrl = () => {
  return ''; // Replace with your actual base URL
};

const Environments: React.FC<EnvironmentsProps> = ({ tenants, onClose }) => {
  const [allEnvironments, setAllEnvironments] = useState<
    { tenantName: string; tenantId: string; environmentName: string; selected: boolean }[]
  >([]);
  const [selectedEnvironments, setSelectedEnvironments] = useState<
    {
      tenantName: string;
      environmentName: string;
      custom_hash?: string;
      rpo_hash?: string;
      custom_timestamp?: string;
      rpo_timestamp?: string;
    }[]
  >([]);
  const [loading, setLoading] = useState(true);
  const [loadingProgress, setLoadingProgress] = useState(0);
  const [showResults, setShowResults] = useState(false);
  const [showCRUD, setShowCRUD] = useState(false); // State to control the CRUD modal visibility
  const [successMessage, setSuccessMessage] = useState<string | null>(null);
  const [errorMessage, setErrorMessage] = useState<string | null>(null);
  const [modalMessage, setModalMessage] = useState<string | null>(null);
  const [selectedProperty, setSelectedProperty] = useState<{ [key: string]: string }>({});
  const [propertyValue, setPropertyValue] = useState<{ [key: string]: string }>({});
  const [properties, setProperties] = useState<{ [key: string]: string }>({});
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [modalAction, setModalAction] = useState<() => void>(() => { });
  const [showInput, setShowInput] = useState<{ [key: string]: boolean }>({}); // Track visibility of input and buttons

  const [showRpoDialog, setShowRpoDialog] = useState<boolean>(false);
  const [originTenantId, setOriginTenantId] = useState<string>('');
  const [destinationTenantId, setDestinationTenantId] = useState<string>('');
  const [originEnv, setOriginEnv] = useState<string>('');
  const [destEnv, setDestEnv] = useState<string>('');
  const [originEnvironments, setOriginEnvironments] = useState<string[]>([]);
  const [destinationEnvironments, setDestinationEnvironments] = useState<string[]>([]);
  const [showWait, setShowWait] = useState<boolean>(false);
  const [showPatchDialog, setShowPatchDialog] = useState<boolean>(false);
  const [userGroup, setUserGroup] = useState<string | null>(null); // Add userGroup state
  const [envSourcePaths, setEnvSourcePaths] = useState<{ [key: string]: string }>({});
  const [refreshKey, setRefreshKey] = useState(0); // Used to trigger refresh

  const refreshEnvironments = () => {
    setRefreshKey(prev => prev + 1);
  };

  const handlePatchDialogClose = () => {
    setShowPatchDialog(false);
    refreshEnvironments();
  };

  const createLogEntry = async (params: logParams) => {
    try {
      const originTenantName = tenants.find(tenant => tenant.id === params.origin_tenant)?.name || '';
      const destinationTenantName = tenants.find(tenant => tenant.id === params.destination_tenant)?.name || '';
      const now = new Date();
      const logEntry = {
        date: now.toISOString().split('T')[0],
        time: now.toTimeString().split(' ')[0],
        user: localStorage.getItem('username') || 'unknown',
        tenant: originTenantName,
        service: params.serviceName,
        action: params.action,
        origin: params.origin,
        origin_tenant: originTenantName,
        destination_tenant: destinationTenantName || '',
        destination: params.destination || '',
        result: 'In Progress',
        log_result: params.log_result
      };
      const response = await axios.post(`${getBaseUrl()}/api/logs`, logEntry);
      return response.data.id;
    } catch (error) {
      console.error('Error creating log entry:', error);
      return null;
    }
  };

  const updateLogEntry = async (logId: number, result: string, full_log?: string) => {
    try {
      const now = new Date();
      await axios.put(`${getBaseUrl()}/api/logs/${logId}`, {
        finish_date: now.toISOString().split('T')[0],
        finish_time: now.toLocaleTimeString('en-US', { hour12: false }),
        result: result,
        log_result: full_log
      });
      if (result === 'Failed') {
        setErrorMessage(`Erro: ${full_log}`);
        setTimeout(() => setErrorMessage(''), 3000); // Hide after 3 seconds
        }
    } catch (error) {
      // alert(`Error updating log entry ${error}`);
      console.error('Error updating log entry:', error);
    }
  };

  useEffect(() => {
    const fetchEnvironments = async () => {
      try {
        const environmentsData: { tenantName: string; tenantId: string; environmentName: string; selected: boolean }[] = [];

        for (const tenant of tenants) {
          try {
            const response = await axios.get<{ environments: string[] }>(
              `${getBaseUrl()}/api/get-environments/${tenant.id}`
            );
            response.data.environments.forEach((environment) => {
              environmentsData.push({
                tenantName: tenant.name,
                tenantId: tenant.id,
                environmentName: environment,
                selected: false, // Default to not selected
              });
            });
          } catch (error) {
            console.error(`Error fetching environments for tenant ${tenant.name}:`, error);
          }
        }

        setAllEnvironments(environmentsData);
      } catch (error) {
        console.error('Error loading environments:', error);
      } finally {
        setLoading(false);
      }
    };

    fetchEnvironments();
  }, [tenants, refreshKey]);

  useEffect(() => {
    const fetchUserGroup = async () => {
      const username = localStorage.getItem('username');
      if (!username) return;

      try {
        const response = await axios.get(`/api/users/${username}`);
        const group = response.data.role;
        setUserGroup(group);
      } catch (error) {
        console.error('Error fetching user group:', error);
      }
    };

    fetchUserGroup();
  }, []);

  const handleCheckboxChange = (index: number) => {
    const updatedEnvironments = [...allEnvironments];
    updatedEnvironments[index].selected = !updatedEnvironments[index].selected;
    setAllEnvironments(updatedEnvironments);
  };

  const fetchSelectedEnvironmentData = async () => {
    setLoading(true);
    setLoadingProgress(0);

    const selectedData = allEnvironments.filter((env) => env.selected);
    const totalCalls = selectedData.length;
    let completedCalls = 0;

    const results: {
      tenantName: string;
      environmentName: string;
      custom_hash?: string;
      rpo_hash?: string;
      custom_timestamp?: string;
      rpo_timestamp?: string;
    }[] = [];

    for (const env of selectedData) {
      try {
        const response = await axios.post<EnvironmentInfo>(
          `${getBaseUrl()}/api/get-rpo-hash`,
          {
            tenantId: env.tenantId,
            environmentId: env.environmentName,
          }
        );
        results.push({
          tenantName: env.tenantName,
          environmentName: env.environmentName,
          custom_hash: response.data.custom_hash,
          rpo_hash: response.data.rpo_hash,
          custom_timestamp: response.data.custom_timestamp,
          rpo_timestamp: response.data.rpo_timestamp,
        });
      } catch (error) {
        // alert(`Error fetching data for environment ${env.environmentName}: ${error}`);
        console.error(`Error fetching data for environment ${env.environmentName}:`, error);
        results.push({
          tenantName: env.tenantName,
          environmentName: env.environmentName,
        });
      }

      completedCalls++;
      setLoadingProgress(Math.floor((completedCalls / totalCalls) * 100));
    }

    setSelectedEnvironments(results);
    setShowResults(true);
    setLoading(false);
  };

  const handleRemoveEnvironment = async () => {
    const selectedEnv = allEnvironments.find((env) => env.selected);

    setSuccessMessage(null);
    setErrorMessage(null);

    if (!selectedEnv) {
      setErrorMessage('Please select an environment to remove.');
      setTimeout(() => setErrorMessage(''), 3000); // Hide after 3 seconds
      return;
    }

    const logId = await createLogEntry({
      serviceName: 'Remove Environment',
      action: 'Removal',
      origin: selectedEnv.environmentName,
      destination: selectedEnv.environmentName,
      origin_tenant: selectedEnv.tenantId,
      destination_tenant: selectedEnv.tenantId,
    });

    try {

      const response = await axios.post(`${getBaseUrl()}/api/remove-environment`, {
        tenantId: selectedEnv.tenantId,
        environment: selectedEnv.environmentName,
      });
      setAllEnvironments((prev) =>
        prev.filter(
          (env) =>
            env.tenantId !== selectedEnv.tenantId || env.environmentName !== selectedEnv.environmentName
        )
      );
      if (logId) await updateLogEntry(logId, 'Success', response.data.message);
      setSuccessMessage('Ambiente removido com sucesso!');
      setTimeout(() => setSuccessMessage(''), 3000); // Hide after 3 seconds
      refreshEnvironments(); // Refresh after removal

    } catch (error) {
      if (logId) {
        const errorMessage = axios.isAxiosError(error) && error.response?.data?.error ? error.response.data.error : 'Unknown error';
        await updateLogEntry(logId, 'Failed', errorMessage);
      }
      // alert(`Falha ao remover ambiente. Verifique o log. ${error}`);
      console.error('Falha ao remover ambiente. Verifique o log.', error);
      setErrorMessage('Falha ao remover ambiente. Verifique o log.');
      setTimeout(() => setErrorMessage(''), 3000); // Hide after 3 seconds
    }
  };

  const fetchProperties = async (tenantId: string, environmentName: string) => {
    try {
      const response = await axios.get<{ [key: string]: string }>(
        `${getBaseUrl()}/api/get-ini-keys/${tenantId}/${environmentName}`
      );
      setProperties(response.data);
    } catch (error) {
      // alert(`Error fetching properties. Please try again later. ${error}`);  
      console.error('Error fetching properties:', error);
      if (axios.isAxiosError(error) && error.response?.data?.error) {
        setErrorMessage(`Erro:${error.response.data.error}`);
      } else {
        setErrorMessage(`Erro:${error instanceof Error ? error.message : 'Unknown error'}`);
      }
      setTimeout(() => setErrorMessage(''), 6000); // Hide after 3 seconds
    }
  };

  const handlePropertySelect = (tenantId: string, environmentName: string) => {
    fetchProperties(tenantId, environmentName);
  };

  const handlePropertyChange = (tenantId: string, environmentName: string, property: string) => {
    setSelectedProperty({ ...selectedProperty, [`${tenantId}-${environmentName}`]: property });
    setPropertyValue({ ...propertyValue, [`${tenantId}-${environmentName}`]: properties[property] });
    setShowInput({ ...showInput, [`${tenantId}-${environmentName}`]: true }); // Show input and buttons
  };

  const handlePropertyValueChange = (tenantId: string, environmentName: string, value: string) => {
    setPropertyValue({ ...propertyValue, [`${tenantId}-${environmentName}`]: value });
  };

  const handlePropertyUpdate = async (tenantId: string, environmentName: string, property: string, value: string) => {
    const environment = allEnvironments.find(env => env.tenantId === tenantId && env.environmentName === environmentName);

    try {
      const response = await axios.get(
        `${getBaseUrl()}/api/environment_access/${tenantId}/${userGroup}/${environmentName}`
      );
      
      // Check if user is admin or has proper access rights
      if (userGroup !== 'admin') {
        const accessData = response.data.find((item: any) => item.allow04 === 1);
        if (!accessData) {
          setModalMessage('Você não tem permissão para alterar as propriedades deste ambiente.');
          setIsModalOpen(true);
          return;
        }
      }

      const endpoint = `/api/update-ini-keys/${tenantId}/${environmentName}`;
      await axios.post(`${getBaseUrl()}${endpoint}`, {
        [property]: value
      });
      
      setSuccessMessage('Propriedade alterada com sucesso!');
      setTimeout(() => setSuccessMessage(''), 3000);

      // Reset state after saving
      setSelectedProperty({ ...selectedProperty, [`${tenantId}-${environmentName}`]: '' });
      setPropertyValue({ ...propertyValue, [`${tenantId}-${environmentName}`]: '' });
      setShowInput({ ...showInput, [`${tenantId}-${environmentName}`]: false });
    } catch (error) {
      console.error('Error changing property:', error);
      setErrorMessage('Falha ao alterar propriedade. Verifique o log.');
      setTimeout(() => setErrorMessage(''), 3000);
    }
  };

  const handleCancel = (tenantId: string, environmentName: string) => {
    // Reset state on cancel
    setSelectedProperty({ ...selectedProperty, [`${tenantId}-${environmentName}`]: '' });
    setPropertyValue({ ...propertyValue, [`${tenantId}-${environmentName}`]: '' });
    setShowInput({ ...showInput, [`${tenantId}-${environmentName}`]: false }); // Hide input and buttons
  };

  const confirmAction = () => {
    modalAction();
    setIsModalOpen(false);
  };

  const cancelAction = () => {
    setIsModalOpen(false);
  };

  const showModal = (action: () => void) => {
    setModalAction(() => action);
    setIsModalOpen(true);
  };

  const handlePropertyChangeWithConfirmation = (tenantId: string, environmentName: string, property: string) => {
    handlePropertyChange(tenantId, environmentName, property);
  };

  const fetchEnvironmentsForTenant = async (tenantId: string, type: 'origin' | 'destination') => {
    if (!tenantId) return;
    try {
      const response = await axios.get<{ environments: string[] }>(`${getBaseUrl()}/api/get-environments/${tenantId}`);
      if (type === 'origin') {
        setOriginEnvironments(response.data.environments || []);
      } else {
        setDestinationEnvironments(response.data.environments || []);
      }
    } catch (error) {
      console.error('Error fetching environments:', error);
    }
  };

  const promoteRpo = async () => {
    try {
      if (
        originTenantId &&
        originEnv &&
        destinationTenantId &&
        destEnv &&
        (originTenantId !== destinationTenantId || originEnv !== destEnv)
      ) {
        // Check if destination topology and environment are allowed
        if (userGroup !== 'admin') {
          try {
            const response = await axios.get(
              `${getBaseUrl()}/api/environment_access/${destinationTenantId}/${userGroup}/${destEnv}`
            );
            const accessData = response.data.find((item: any) => item.allow01 === 1);

            if (!accessData) {
              setModalMessage('Você não tem permissão para promover o RPO neste ambiente.');
              setIsModalOpen(true);
              return;
            }
          } catch (error) {
            console.error('Error checking environment access:', error);
            setErrorMessage('Erro ao verificar permissões para o ambiente de destino.');
            return;
          }
        }

        const promoteData = {
          origin_tenant: originTenantId,
          origin: originEnv,
          destination_tenant: destinationTenantId,
          destination: destEnv,
        };
        const logId = await createLogEntry({
          serviceName: 'Promote RPO',
          action: 'Promote',
          origin: originEnv,
          destination: destEnv,
          origin_tenant: originTenantId,
          destination_tenant: destinationTenantId,
        });

        try {
          setShowWait(true);
          const result = await axios.post(`${getBaseUrl()}/api/promote`, promoteData);
          setSuccessMessage(
            `RPO promovido de ${originEnv} (Topologia ${originTenantId}) para ${destEnv} (Topologia ${destinationTenantId}).`
          );
          setTimeout(() => setSuccessMessage(''), 3000); // Hide after 3 seconds
          if (logId) await updateLogEntry(logId, 'Success', result.data?.message);
          refreshEnvironments(); // Refresh after promote
        } catch (error) {
          console.error('Error promoting RPO:', error);
          if (axios.isAxiosError(error) && error.response?.data?.error) {
            setErrorMessage(`Error promoting RPO: ${error.response.data.error}`);
            if (logId) await updateLogEntry(logId, 'Failed', error.response.data.error);
          } else {
            setErrorMessage(`Error promoting RPO: ${String(error)}`);
            if (logId) await updateLogEntry(logId, 'Failed', String(error));
          }
          setTimeout(() => setErrorMessage(''), 3000); // Hide after 3 seconds
        } finally {
          setShowWait(false);
          setShowRpoDialog(false);
        }
      } else {
        setErrorMessage('Topologia e Environment de origem e destino devem ser diferentes.');
        setTimeout(() => setErrorMessage(''), 3000); // Hide after 3 seconds        
      }
    } catch (error) {
      console.error('Error promoting RPO:', error);
    }
  };

  const checkEnvironmentPermissions = async (tenantId: string, environment: string): Promise<boolean> => {
    if (userGroup === 'admin') return true;
    
    try {
      const response = await axios.get(
        `${getBaseUrl()}/api/environment_access/${tenantId}/${userGroup}/${environment}`
      );
      const accessData = response.data.find((item: any) => item.allow03 === 1);
      return !!accessData;
    } catch (error) {
      console.error('Error checking environment permissions:', error);
      return false;
    }
  };

  const handleNewEnvironmentClick = () => {
    setShowCRUD(true);
  };

  const handleRemoveEnvironmentClick = async () => {
    const selectedEnv = allEnvironments.find((env) => env.selected);
    setModalMessage(null);

    if (!selectedEnv) {
      setModalMessage('Por favor, selecione um ambiente para remover.');
      setIsModalOpen(true);
      return;
    }

    // Check permissions using the new function
    const hasPermission = await checkEnvironmentPermissions(selectedEnv.tenantId, selectedEnv.environmentName);
    if (!hasPermission) {
      setModalMessage('Você não tem permissão para remover este ambiente.');
      setIsModalOpen(true);
      return;
    }

    // Show confirmation modal for allowed removal
    setModalMessage('Tem certeza de que deseja remover este ambiente?');
    setModalAction(() => handleRemoveEnvironment);
    setIsModalOpen(true);
  };

  const fetchSourcePathForEnv = async (tenantId: string, environmentName: string) => {
    try {
      const response = await axios.get<{ [key: string]: string }>(
        `${getBaseUrl()}/api/get-ini-keys/${tenantId}/${environmentName}`
      );
      const sourcepath = response.data['sourcepath'] || '';
      setEnvSourcePaths(prev => ({
        ...prev,
        [`${tenantId}-${environmentName}`]: sourcepath
      }));
    } catch (error) {
      setEnvSourcePaths(prev => ({
        ...prev,
        [`${tenantId}-${environmentName}`]: ''
      }));
    }
  };

  // Fetch sourcepath for all environments after environments are loaded
  useEffect(() => {
    if (allEnvironments.length > 0) {
      allEnvironments.forEach(env => {
        fetchSourcePathForEnv(env.tenantId, env.environmentName);
      });
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [allEnvironments]);

  return (
    <div className={styles.environments}>
      {loading ? (
        <div className={styles.loadingContainer}>
          <p>Carregando, aguarde... {loadingProgress}%</p>
          <div className={styles.progressBar}>
            <div
              className={styles.progress}
              style={{ width: `${loadingProgress}%` }}
            ></div>
          </div>
        </div>
      ) : showResults ? (
        <>
          <button className={styles.button} type="button" onClick={() => setShowResults(false)}>
            Voltar
          </button>
          <table className={styles.table}>
            <thead>
              <tr>
                <th>Topologia</th>
                <th>Ambiente</th>
                <th>RPO Timestamp</th>
                <th>RPO Hash</th>
                <th>Custom Timestamp</th>
                <th>Custom Hash</th>
              </tr>
            </thead>
            <tbody>
              {selectedEnvironments.map((env, index) => {
                // Compare with the first line
                const first = selectedEnvironments[0];
                const isEqual =
                  index !== 0 &&
                  env.rpo_hash === first.rpo_hash &&
                  env.custom_hash === first.custom_hash;
                return (
                  <tr key={index} style={isEqual ? { fontWeight: 'bold' } : {}}>
                    <td>{env.tenantName}</td>
                    <td>{env.environmentName}</td>
                    <td>{env.rpo_timestamp || 'n/a'}</td>
                    <td>{env.rpo_hash || 'n/a'}</td>
                    <td>{env.custom_timestamp || 'n/a'}</td>
                    <td>{env.custom_hash || 'n/a'}</td>
                  </tr>
                );
              })}
            </tbody>
          </table>
        </>
      ) : (
        <>
          <button className={styles.button} type="button" onClick={fetchSelectedEnvironmentData}>
            Informações
          </button>
          <button className={styles.button} type="button" onClick={handleNewEnvironmentClick}>
            Novo Ambiente
          </button>
          <button className={styles.button} type="button" onClick={handleRemoveEnvironmentClick}>
            Remover Ambiente
          </button>
          <button
            className={styles.button}
            type="button"
            onClick={() => {
              setOriginTenantId('');
              setOriginEnv('');
              setDestinationTenantId('');
              setDestEnv('');
              setShowRpoDialog(true);
            }}
          >
            Promover RPO
          </button>
          <button className={styles.button} type="button" onClick={() => setShowPatchDialog(true)}>
            Aplicar Patch
          </button>

          {showCRUD && <EnvironmentCRUD tenants={tenants} onClose={() => { setShowCRUD(false); refreshEnvironments(); }} />}
          <table className={styles.table}>
            <thead>
              <tr>
                <th>Selecionar</th>
                <th>Topologia</th>
                <th>Ambiente</th>
                <th>Propriedades</th>
                <th>SourcePath</th>
              </tr>
            </thead>
            <tbody>
              {allEnvironments.map((env, index) => (
                <tr key={index}>
                  <td>
                    <input
                      type="checkbox"
                      checked={env.selected}
                      onChange={() => {
                        handleCheckboxChange(index);
                      }}
                    />
                  </td>
                  <td>{env.tenantName}</td>
                  <td>{env.environmentName}</td>
                  <td>
                    <select
                      value={selectedProperty[`${env.tenantId}-${env.environmentName}`] || ''}
                      onFocus={() => handlePropertySelect(env.tenantId, env.environmentName)}
                      onChange={(e) => handlePropertyChangeWithConfirmation(env.tenantId, env.environmentName, e.target.value)}
                    >
                      <option value="">Selecionar</option>
                      {Object.keys(properties).map((key) => (
                        <option key={key} value={key}>
                          {key}
                        </option>
                      ))}
                    </select>
                    {showInput[`${env.tenantId}-${env.environmentName}`] && (
                      <div>
                        <input
                          type="text"
                          value={propertyValue[`${env.tenantId}-${env.environmentName}`] || ''}
                          onChange={(e) => handlePropertyValueChange(env.tenantId, env.environmentName, e.target.value)}
                        />
                        <button
                          className={styles.button} // Apply the same button styling
                          onClick={() => handlePropertyUpdate(env.tenantId, env.environmentName, selectedProperty[`${env.tenantId}-${env.environmentName}`], propertyValue[`${env.tenantId}-${env.environmentName}`])}
                        >
                          Save
                        </button>
                        <button
                          className={styles.button} // Apply the same button styling
                          onClick={() => handleCancel(env.tenantId, env.environmentName)}
                        >
                          Cancel
                        </button>
                      </div>
                    )}
                  </td>
                  <td>
                    {envSourcePaths[`${env.tenantId}-${env.environmentName}`] || ''}
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </>
      )}
      {showRpoDialog && (
        <div className="overlay">
          <div className="rpo-dialog">
            <h3>Promover RPO</h3>
            <label>
              Topologia Origem:
              <select
                value={originTenantId}
                onChange={(e) => {
                  const selectedTenantId = e.target.value;
                  setOriginTenantId(selectedTenantId);
                  setOriginEnv(''); // Reset origin environment
                  fetchEnvironmentsForTenant(selectedTenantId, 'origin');
                }}
              >
                <option value="">Selecione</option>
                {tenants.map((tenant) => (
                  <option key={tenant.id} value={tenant.id}>
                    {tenant.name}
                  </option>
                ))}
              </select>
            </label>
            <label>
              RPO Origem:
              <select
                value={originEnv}
                onChange={(e) => setOriginEnv(e.target.value)}
              >
                <option value="">Selecione</option>
                {originEnvironments.map((environment, index) => (
                  <option key={index} value={environment}>
                    {environment}
                  </option>
                ))}
              </select>
            </label>
            <label>
              Topologia Destino:
              <select
                value={destinationTenantId}
                onChange={(e) => {
                  const selectedTenantId = e.target.value;
                  setDestinationTenantId(selectedTenantId);
                  setDestEnv(''); // Reset "RPO Destino"
                  fetchEnvironmentsForTenant(selectedTenantId, 'destination');
                }}
              >
                <option value="">Selecione</option>
                {tenants.map((tenant) => (
                  <option key={tenant.id} value={tenant.id}>
                    {tenant.name}
                  </option>
                ))}
              </select>
            </label>
            <label>
              RPO Destino:
              <select
                value={destEnv}
                onChange={async (e) => {
                  const selectedEnv = e.target.value;
                  if (selectedEnv && destinationTenantId && userGroup) {
                    try {
                      const response = await axios.get(
                        `${getBaseUrl()}/api/environment_access/${destinationTenantId}/${userGroup}/${selectedEnv}`
                      );
                      const accessData = response.data.find((item: any) => item.allow01 === 1);

                      if ((!accessData) && (userGroup != 'admin')) {
                        setModalMessage('Você não tem permissão para selecionar este ambiente como RPO Destino.');
                        setDestEnv(''); // Reset the selection
                        setIsModalOpen(true);
                        return;
                      }
                    } catch (error) {
                      console.error('Error checking environment access:', error);
                      setErrorMessage('Erro ao verificar permissões para o ambiente de destino.');
                      setDestEnv(''); // Reset the selection
                      setIsModalOpen(true);
                      return;
                    }
                  }
                  setDestEnv(selectedEnv); // Set the selection if allowed
                }}
              >
                <option value="">Selecione</option>
                {destinationEnvironments.map((environment, index) => (
                  <option key={index} value={environment}>
                    {environment}
                  </option>
                ))}
              </select>
            </label>
            <div className="button-container">
              <button disabled={showWait} onClick={promoteRpo}>Executar</button>
              <button disabled={showWait} onClick={() => setShowRpoDialog(false)}>Cancelar</button>
            </div>
          </div>
        </div>
      )}
      {showPatchDialog && (
        <div className="patch-dialog">
          <PatchApplyForm tenants={tenants} onClose={handlePatchDialogClose} />
        </div>
      )}
      <div>
        {successMessage && <div className={styles.successballoon}>{successMessage}</div>}
        {errorMessage && <div className={styles.failedballoon}>{errorMessage}</div>}
      </div>
      <ConfirmationModal
        isOpen={isModalOpen}
        title={modalMessage === 'Tem certeza de que deseja remover este ambiente?' ? 'Confirmação' : 'Permissão Negada'}
        message={modalMessage || ''}
        onConfirm={modalMessage === 'Tem certeza de que deseja remover este ambiente?' ? confirmAction : () => setIsModalOpen(false)}
        onCancel={() => setIsModalOpen(false)}
      />
    </div>
  );
};

export default Environments;