import React, { useEffect, useState } from 'react';
import axios from 'axios';
import { PieChart, Pie, Cell, BarChart, Bar, XAxis, YAxis, Tooltip, Legend, ResponsiveContainer } from 'recharts';
import './Monitor.css';

interface ServerInfo {
  cpu: { percent: number };
  disk: { free: number; percent: number; total: number; used: number };
  memory: { available: number; free: number; percent: number; total: number; used: number };
}

const Monitor: React.FC = () => {
  const [serverInfo, setServerInfo] = useState<ServerInfo | null>(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    const fetchServerInfo = async () => {
      try {
        const response = await axios.get<ServerInfo>('/api/server-info');
        setServerInfo(response.data);
      } catch (err: unknown) {
        setError(err instanceof Error ? err.message : 'Erro desconhecido');
        console.error('Erro ao buscar informações do servidor:', err);
      } finally {
        setLoading(false);
      }
    };

    fetchServerInfo();
  }, []);

  if (loading) return <div>Carregando informações do servidor...</div>;
  if (error) return <div className="error">{error}</div>;
  if (!serverInfo) return <div>Nenhum dado disponível</div>;

  // Preparar dados para os gráficos
  const memoryData = [
    { name: 'Usado (GB)', value: (serverInfo.memory.used ?? 0) / 1024 ** 3 },
    { name: 'Livre (GB)', value: (serverInfo.memory.free ?? 0) / 1024 ** 3 },
  ];

  const diskData = [
    { name: 'Usado (GB)', value: (serverInfo.disk.used ?? 0) / 1024 ** 3 },
    { name: 'Livre (GB)', value: (serverInfo.disk.free ?? 0) / 1024 ** 3 },
  ];

  const cpuData = [
    { name: 'Em Uso', value: serverInfo.cpu.percent ?? 0 },
    { name: 'Livre', value: 100 - (serverInfo.cpu.percent ?? 0) },
  ];

  const colors = ['#FF6384', '#36A2EB', '#FFCE56', '#4BC0C0'];

  return (
    <div className="monitor-container">
      <h2>Monitor</h2>
      <div className="chart-container">
        <div className="chart-item">
          <h3>Memória</h3>
          <ResponsiveContainer width="100%" height={300}>
            <PieChart>
              <Pie
                data={memoryData}
                cx="50%"
                cy="50%"
                labelLine={false}
                outerRadius={80}
                fill="#8884d8"
                dataKey="value"
                label={({ name, percent }: { name: string; percent: number }) =>
                  `${name}: ${(percent * 100).toFixed(2)}%`
                }
              >
                {memoryData.map((entry, index) => (
                  <Cell key={`cell-${index}`} fill={colors[index % colors.length]} />
                ))}
              </Pie>
              <Tooltip />
              <Legend />
            </PieChart>
          </ResponsiveContainer>
        </div>

        <div className="chart-item">
          <h3>Uso de Disco</h3>
          <ResponsiveContainer width="100%" height={300}>
            <PieChart>
              <Pie
                data={diskData}
                cx="50%"
                cy="50%"
                labelLine={false}
                outerRadius={80}
                fill="#8884d8"
                dataKey="value"
                label={({ name, percent }: { name: string; percent: number }) =>
                  `${name}: ${(percent * 100).toFixed(2)}%`
                }
              >
                {diskData.map((entry, index) => (
                  <Cell key={`cell-${index}`} fill={colors[index % colors.length]} />
                ))}
              </Pie>
              <Tooltip />
              <Legend />
            </PieChart>
          </ResponsiveContainer>
        </div>

        <div className="chart-item">
          <h3>CPU (%)</h3>
          <ResponsiveContainer width="100%" height={300}>
            <BarChart data={cpuData}>
              <XAxis dataKey="name" />
              <YAxis />
              <Tooltip />
              <Legend />
              <Bar dataKey="value" fill="#8884d8" />
            </BarChart>
          </ResponsiveContainer>
        </div>
      </div>
    </div>
  );
};

export default Monitor;