import React, { useState, useEffect } from 'react';
import './UserEditModal.css';
import axios from 'axios';
import { useForm } from 'react-hook-form';

interface User {
  username: string;
  name: string;
  role: string;
  email: string;
  company: string;
  password?: string; // Optional password
}

interface Group {
  active: number;
  group_id: string;
  name: string;
}

interface UserEditProps {
  user: User;
  onSave: (user: User) => void;
  onClose: () => void;
  option?: string;
}

const UserEdit: React.FC<UserEditProps> = ({ user, onSave, onClose, option }) => {
  const [formData, setFormData] = useState<User>({
    ...user,
    password: '', // Initialize password field explicitly
  });
  const [confirmPassword, setConfirmPassword] = useState<string>('');
  const [successMessage, setSuccessMessage] = useState<string | null>(null);
  const [errorMessage, setErrorMessage] = useState<string | null>(null);
  const [isAdmin, setIsAdmin] = useState<boolean>(false);
  const [groups, setGroups] = useState<Group[]>([]);
  const [userGroup, setUserGroup] = useState<string | null>(null);
  const { register, formState: { errors } } = useForm<User>({
    defaultValues: user
  });

  // Check if the user is an admin
  useEffect(() => {
    const username = localStorage.getItem('username');
    if (username?.toLowerCase() === 'admin') {
      setIsAdmin(true);
    }
  }, []);

  // Synchronize formData with user prop when it changes
  useEffect(() => {
    setFormData({ ...user, password: '' }); // Reset password to an empty value

    setSuccessMessage(null);
    setErrorMessage(null);

  }, [user]);

  useEffect(() => {
    const fetchGroups = async () => {
      try {
        const response = await axios.get<Group[]>('/api/groups');
        setGroups(response.data);
      } catch (error) {
        console.error('Error fetching groups:', error);
      }
    };

    fetchGroups();
  }, []);

  // Add useEffect to fetch user group
  useEffect(() => {
    const fetchUserGroup = async () => {
      const username = localStorage.getItem('username');
      if (!username) return;

      try {
        const response = await axios.get(`/api/users/${username}`);
        const group = response.data.role;
        setUserGroup(group);
      } catch (error) {
        console.error('Error fetching user group:', error);
      }
    };

    fetchUserGroup();
  }, []);

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target;
    setFormData(prevState => ({
      ...prevState,
      [name]: value,
    }));
  };

  const handlePasswordChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setFormData(prevState => ({
      ...prevState,
      password: e.target.value,
    }));
  };

  const handleConfirmPasswordChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setConfirmPassword(e.target.value);
  };

  const handleSubmit2 = () => {
    if (formData.password && formData.password !== confirmPassword) {
      alert("Passwords do not match!");
      return;
    }
    try {
      onSave(formData); // Pass updated data back to parent
    }
    catch (error) {
      console.error('Erro ao salvar usuário:', error);
    }
  };

  const handleCancel = () => {
    onClose();
  };

  return (
    <div className="user-edit-container">
      <div className="user-edit-content">
        <h2 className="edit-header">{option}</h2>
        <div className="form-group">
          <label className="form-label">Username</label>
          <input
            className="username-field"
            type="text"
            name="username"
            value={formData.username}
            onChange={handleChange}
            disabled={option === 'Alterando'}
          />
          <div className="username-separator"></div>
          <div className="form-row">
            <div className="form-field">
              <label className="form-label">Nome</label>
              <input className="form-input"
                type="text"
                name="name"
                value={formData.name}
                onChange={handleChange}
              />
            </div>
            <div className="form-field">
              <label className="form-label">Grupo</label>
              <select className="form-input"
                {...register("role", { required: "Grupo Obrigatório" })}
                value={formData.role}
                onChange={(e) => setFormData(prevState => ({ ...prevState, role: e.target.value }))}
                disabled={userGroup !== 'admin'}
              >
                <option value="">Selecione o Grupo</option>
                {groups.map((group) => (
                  <option key={group.group_id} value={group.group_id}>
                    {group.name}
                  </option>
                ))}
              </select>
            </div>
          </div>
          {errors.role && <span className="error">{errors.role.message}</span>}
        </div>
        <div className="form-row">
          <div className="form-field-60">
            <label className="form-label">e-mail</label>
            <input className="form-input"
              type="text"
              name="email"
              value={formData.email}
              onChange={handleChange}
            />
          </div>
          <div className="form-field-40">
            <label className="form-label">Empresa</label>
            <input className="form-input"
              type="text"
              name="company"
              value={formData.company}
              onChange={handleChange}
            />
          </div>
        </div>
        <div className="form-group-password">
          <div className="password-input-group">
            <label className="form-label-password">Senha:</label>
            <input
              type="password"
              name="password"
              value={formData.password}
              onChange={handlePasswordChange}
            />
          </div>
          <div className="password-input-group">
            <label className="form-label-password">Confirme:</label>
            <input
              type="password"
              name="confirmPassword"
              value={confirmPassword}
              onChange={handleConfirmPasswordChange}
            />
          </div>
        </div>
        <div>
          {successMessage && <div className='successballoon2'>{successMessage}</div>}
          {errorMessage && <div className='failedballoon2'>{errorMessage}</div>}
        </div>
        <div className="action-buttons">
          <button className="add-user-button" onClick={handleSubmit2}>Gravar</button>
          <button className="add-user-button" onClick={handleCancel}>Cancelar</button>
        </div>
      </div>
    </div>
  );
};

export default UserEdit;
