import React, { useEffect, useState } from 'react';
import axios from 'axios';
import UserEditModal from './UserEditModal';
import './UserManagement.css';
import ConfirmationModal from "../ConfirmationModal/ConfirmationModal";

interface User {
  username: string;
  name: string;
  role: string;
  email: string;
  company: string;
}

const UserManagement: React.FC = () => {
  const [users, setUsers] = useState<User[]>([]);
  const [selectedUser, setSelectedUser] = useState<User | null>(null);
  const [isModalConfirmationOpen, setIsModalConfirmationOpen] = useState<boolean>(false);
  const [isModalEditOpen, setIsModalEditOpen] = useState<boolean>(false);
  const [isNewUser, setIsNewUser] = useState<boolean>(false);
  const [isAdmin, setIsAdmin] = useState<boolean>(false);
  const [modalActionConfirmation, setmodalActionConfirmation] = useState<() => void>(() => { });

  // Check if the user is an admin
  useEffect(() => {
    const username = localStorage.getItem('username');
    if (username?.toLowerCase() === 'admin') {
      setIsAdmin(true);
    }
  }, []);

  // Fetch the list of users
  useEffect(() => {
    const fetchUsers = async () => {
      try {
        const response = await axios.get<User[]>('/api/users');
        setUsers(response.data);
      } catch (error) {
        console.error('Error fetching users:', error);
      }
    };

    if (isAdmin) {
      fetchUsers();
    }
  }, [isAdmin]);

  const handleEditUser = (user: User) => {
    setSelectedUser(user);
    setIsNewUser(false);
    setIsModalEditOpen(true);
  };

  const handleAddUser = () => {
    const newUser: User = {
      username: '',
      name: '',
      role: '',
      email: '',
      company: ''
    };

    setSelectedUser(newUser);
    setIsNewUser(true);
    setIsModalEditOpen(true);
  };

  const confirmAction = () => {
    modalActionConfirmation();
    setIsModalEditOpen(false);
  };

  const cancelAction = () => {
    setIsModalConfirmationOpen(false);
  };

  const showModalConfirmation = (action: () => void) => {
    setmodalActionConfirmation(() => action);
    setIsModalConfirmationOpen(true);
  };

  const handleDeleteUser = async (username: string) => {
    showModalConfirmation(() => {
      try {
        axios.delete(`/api/users/${username}`);
        setUsers(users.filter(user => user.username !== username));
      } catch (error) {
        console.error('Error deleting user:', error);
      }
      setIsModalConfirmationOpen(false);
    });
  };

  const handleSaveUser = async (user: User) => {
    try {
      if (isNewUser) {
        await axios.post('/api/users', user);
        setUsers([...users, user]);
      } else {
        await axios.put(`/api/users/${user.username}`, user);
        setUsers(users.map(u => (u.username === user.username ? user : u)));
      }
      setIsModalEditOpen(false);
    } catch (error) {
      console.error('Error saving user:', error);
    }
  };

  const handleCloseModal = () => {
    setIsModalEditOpen(false);
  };

  // Render nothing if the user is not an admin
  if (!isAdmin) {
    return null;
  }

  return (
    <>
      {isModalEditOpen && selectedUser ? (
        <UserEditModal
          user={selectedUser}
          onSave={handleSaveUser}
          onClose={handleCloseModal}
          option={isNewUser ? 'Incluindo' : 'Alterando'}
        />
      ) : (
        <div className="user-management">
          <div className="user-management-buttons">
            <button className="add-user-button" onClick={handleAddUser}>
              Incluir
            </button>
          </div>
          <table className="user-table">
            <thead>
              <tr>
                <th>Username</th>
                <th>Nome</th>
                <th>Grupo</th>
                <th>Email</th>
                <th>Empresa</th>
                <th>Ações</th>
              </tr>
            </thead>
            <tbody>
              {users.map(user => (
                <tr key={user.username}>
                  <td>{user.username}</td>
                  <td>{user.name}</td>
                  <td>{user.role}</td>
                  <td>{user.email}</td>
                  <td>{user.company}</td>
                  <td>
                    <button className="edit-button" onClick={() => handleEditUser(user)}>
                      Alterar
                    </button>
                    <button disabled={user.username === 'admin'}
                      className="delete-button"
                      onClick={() => handleDeleteUser(user.username)}
                    >
                      Excluir
                    </button>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      )}
      <ConfirmationModal
        isOpen={isModalConfirmationOpen}
        title="Confirmação Operação?"
        message=""
        onConfirm={confirmAction}
        onCancel={cancelAction}
      />
    </>
  );
};

export default UserManagement;
