import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { useNavigate } from 'react-router-dom';
import './Login.css';
import logo from '../../assets/images/logo.png';

// Token refresh function
const refreshToken = async () => {
  try {
    const refresh_token = localStorage.getItem('refresh_token');
    if (!refresh_token) {
      window.location.href = '/';
      throw new Error('No refresh token available');
    };

    const response = await axios.post('/api/refresh-token', { refresh_token });
    const access_token = response.data.access_token;
    const new_refresh_token = response.data.refresh_token;

    localStorage.setItem('access_token', access_token);
    localStorage.setItem('refresh_token', new_refresh_token);
    return access_token;
  } catch (error) {
    window.location.href = '/';
    throw new Error('Unable to refresh token');
  }
};

// Axios interceptor setup
axios.interceptors.request.use(
  async (config) => {
    let access_token = localStorage.getItem('access_token');
    if (access_token) {
      config.headers['Authorization'] = `Bearer ${access_token}`;
    }
    config.timeout = 60000 * 3; // 3 minutes
    return config;
  },
  (error) => {
    return Promise.reject(error);
  }
);

axios.interceptors.response.use(
  (response) => response,
  async (error) => {
    const originalRequest = error.config;

    if (error.response?.status === 401 && !originalRequest._retry) {
      originalRequest._retry = true;
      const newAccessToken = await refreshToken();

      if (newAccessToken) {
        localStorage.setItem('access_token', newAccessToken);
        originalRequest.headers['Authorization'] = `Bearer ${newAccessToken}`;
        return axios(originalRequest);
      }
    }
    return Promise.reject(error);
  }
);

const Login: React.FC = () => {
  const [username, setUsername] = useState<string>('');
  const [password, setPassword] = useState<string>('');
  const [error, setError] = useState<string>('');
  const navigate = useNavigate();

  useEffect(() => {
    return () => { };
  }, []);

  const handleLogin = async (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    try {
      const response = await axios.post('/api/oauth', {
        username,
        password,
      });

      const { access_token, refresh_token, expires_on, tenant_code, tenant_name, tenant_authorization, tenant_logo } = response.data;
      localStorage.setItem('access_token', access_token);
      localStorage.setItem('refresh_token', refresh_token);
      localStorage.setItem('expires_on', expires_on);
      localStorage.setItem('username', username);
      localStorage.setItem('tenant_code', tenant_code);
      localStorage.setItem('tenant_name', tenant_name);
      localStorage.setItem('tenant_authorization', tenant_authorization);
      localStorage.setItem('tenant_logo', tenant_logo);

      navigate('/deskflow');
    } catch (error: unknown) {
      if (axios.isAxiosError(error) && error.response?.status === 401) {
        setError('Usuário e/ou senha inválidos');
      } else {
        setError('Falha de conexão. Tente novamente mais tarde.');
      }
    }
  };
  return (
    <div className="login-container">
      <form onSubmit={handleLogin}>
        <div className="login-header">
          <div className="deskflowLogin">Login</div>
        </div>
        <div className="deskflowLoginIntro">
              Entre para continuar
        </div>
        <input
          type="text"
          placeholder="Username"
          value={username}
          onChange={(e) => setUsername(e.target.value)}
        />
        <input
          type="password"
          placeholder="Password"
          value={password}
          onChange={(e) => setPassword(e.target.value)}
        />
        {error && <p className="error">{error}</p>}
        <button type="submit">Login</button>
        <div>
          <div className="deskflowLoginRodape1">Powered by</div>
          <div className="deskflowLoginRodape2">© DeskFlow
          <img src={logo} alt="Logo" className="login-logo" />
          </div>
      </div>
      </form>
    </div>
  );
};

export default Login;
