import React, { useEffect, useState } from 'react';
import axios from 'axios';
import './HeaderBar.css';
import socket, { Message } from "../.././socket";
import ConfirmationModal from "../ConfirmationModal/ConfirmationModal";

interface HeaderBarProps {
  username: string;
}

const HeaderBar: React.FC<HeaderBarProps> = ({ username }) => {
  const [fullName, setFullName] = useState('');
  const [successMessage, setSuccessMessage] = useState<string | null>(null);
  const [isAdmin, setIsAdmin] = useState(false);
  const [isModalConfirmationOpen, setIsModalConfirmationOpen] = useState<boolean>(false);
  const [modalActionConfirmation, setmodalActionConfirmation] = useState<() => void>(() => { });
  const [messages, setMessages] = useState<Message[]>([]);

  const handleLogout = () => {
    localStorage.removeItem('token');
    localStorage.removeItem('access_token');
    localStorage.removeItem('refresh_token');
    localStorage.removeItem('username'); // Clear the username as well
    localStorage.removeItem('expires_on');
    localStorage.removeItem('tenant_code');
    localStorage.removeItem('tenant_name');
    localStorage.removeItem('tenant_authorization');
    localStorage.removeItem('tenant_logo');

    window.location.href = '/'; // Redirect to the login page
  };

  const showModalConfirmation = (action: () => void) => {
    setmodalActionConfirmation(() => action);
    setIsModalConfirmationOpen(true);
  };

  const cancelAction = () => {
    setIsModalConfirmationOpen(false);
  };

  const confirmAction = () => {
    modalActionConfirmation();
  };

  useEffect(() => {
    setSuccessMessage(null);

    // When the component mounts, set up Socket.IO listeners
    socket.on("connect", () => {
      console.log("Connected to the server via Socket.IO");
    });

    // Listen for incoming messages from the server
    const handleMessage = (message: Message) => {
      if (message.data.user_id && message.data.user_id !== username) {
        setMessages((prevMessages) => [...prevMessages, message]);
        setSuccessMessage(`${message.data.user_id} : ${message.data.content}`);
        setTimeout(() => setSuccessMessage(''), 10000); // Hide after 3 seconds
      }

    };

    socket.on("alert", handleMessage);
    socket.on("forum", handleMessage);
    socket.on("connect_error", (error) => {
      console.error("Connection error:", error);
    });

    // Clean up listeners on unmount
    return () => {
      socket.off("connect");
      socket.off("connect_error");
    };
  }, []);

  useEffect(() => {
    const fetchFullName = async () => {
      try {
        const response = await axios.get(`/api/users/${username}`);
        setFullName(response.data.name);
        setIsAdmin(response.data.role === 'admin'); // Assuming the response contains an isAdmin field
      } catch (error) {
        console.error('Error fetching user info:', error);
      }
    };

    fetchFullName();
  }, [username]);

  const handleUpdateApp = async () => {

    showModalConfirmation(() => {
      try {
        axios.post('/api/update');
      } catch (error) {
        console.error('Error updating:', error);
      } finally {
        setIsModalConfirmationOpen(false);
      }
    })
  };

  return (
    <div className="header-bar">
      {localStorage.getItem('tenant_logo') && localStorage.getItem('tenant_logo') !== 'null' && (
        <div className="header-logo-container">
          <img
            src={`data:image/png;base64,${localStorage.getItem('tenant_logo')}`}
            alt="logo"
            className="header-logo"
            style={{ width: '110px', height: 'auto', marginLeft: '20px' }}
          />
        </div>
      )}
      <div className="app-client"><h4>{localStorage.getItem('tenant_name') || ''}</h4></div>
      <div className="config-menu">
        {successMessage && <div className='trayMessageStyle'>{successMessage}</div>}
        <button className="config-button">{fullName || username}</button>
        <div className="dropdown-content">
          {isAdmin && <button onClick={handleUpdateApp}>Atualizar App</button>}
          {<button onClick={handleLogout}>Sair</button>}
        </div>
      </div>
      <ConfirmationModal
        isOpen={isModalConfirmationOpen}
        title="Confirmação Atualização da Aplicação?"
        message=""
        onConfirm={confirmAction}
        onCancel={cancelAction}
      />
    </div>
  );
};

export default HeaderBar;