import React, { useState, useEffect, useCallback } from 'react';
import axios from 'axios';
import ConfirmationModal from "../ConfirmationModal/ConfirmationModal";
import './ToggleSwitch.css';
import './Groups.css';

const GroupsManagement = () => {
    interface Group {
        group_id: string;
        name: string;
        active: number;
    }

    interface Topology {
        topology_id: string; // Update to match the API response
        name: string;
    }

    interface TopologyWithPermissions extends Topology {
        allow01?: number;
    }

    interface EnvironmentWithPermissions {
        environment: string;
        allow01: number;
        allow02: number;
        allow03: number;
        allow04: number;
        allow05: number;
    }

    type PermissionKey = 'allow01' | 'allow02' | 'allow03' | 'allow04' | 'allow05';

    const [groups, setGroups] = useState<Group[]>([]);
    const [showModal, setShowModal] = useState(false);
    const [currentGroup, setCurrentGroup] = useState({ group_id: '', name: '', active: 0 });
    const [loading, setLoading] = useState(false);
    const [addingGroup, setAddingGroup] = useState(false);
    const [error, setError] = useState('');
    const [isModalConfirmationOpen, setIsModalConfirmationOpen] = useState<boolean>(false);
    const [modalActionConfirmation, setmodalActionConfirmation] = useState<() => void>(() => { });

    const [topologies, setTopologies] = useState<Topology[]>([]); // Ensure topologies is initialized as an array
    const [selectedTopology, setSelectedTopology] = useState(''); // Selected topology for linking
    const [linkedEnvironments, setLinkedEnvironments] = useState<EnvironmentWithPermissions[]>([]); // Environments linked to the group
    const [linkedTopologies, setLinkedTopologies] = useState<TopologyWithPermissions[]>([]); // Topologies linked to the group

    const [showAccessModal, setShowAccessModal] = useState(false); // State for the new "Acessos" modal

    const [permissions, setPermissions] = useState({
        allow01: true,
        allow02: true,
        allow03: true,
        allow04: false,
        allow05: false,
    });

    const [selectedEnvironment, setSelectedEnvironment] = useState<string>('');
    const [availableEnvironments, setAvailableEnvironments] = useState<EnvironmentWithPermissions[]>([]);

    const fetchGroups = useCallback(async () => {
        setLoading(true);
        try {
            const response = await axios.get('/api/groups');
            setGroups(response.data);
        } catch (error) {
            console.error('Error fetching groups:', error);
            setError('Error fetching groups');
        } finally {
            setLoading(false);
        }
    }, []);

    const fetchTopologies = useCallback(async () => {
        try {
            const response = await axios.get<{ tenants: { id: string; name: string }[] }>('/api/get-tenants');
            const formattedTopologies = response.data.tenants.map((tenant) => ({
                topology_id: tenant.id, // Map 'id' to 'topology_id'
                name: tenant.name,
            }));
            setTopologies(formattedTopologies);
        } catch (error) {
            console.error('Error fetching tenants:', error);
            setError('Error fetching tenants');
        }
    }, []);

    const fetchEnvironments = useCallback(async (tenantId: string) => {
        try {
            const response = await axios.get<{ environments: string[] }>(`/api/get-environments/${tenantId}`);
            const envWithPermissions: EnvironmentWithPermissions[] = response.data.environments.map(env => ({
                environment: env,
                allow01: 0,
                allow02: 0,
                allow03: 0,
                allow04: 0,
                allow05: 0
            }));
            setAvailableEnvironments(envWithPermissions);
            setLinkedEnvironments([]); // Reset linked environments when changing topology
        } catch (error) {
            console.error('Error fetching environments:', error);
            setError('Error fetching environments');
        }
    }, []);

    const fetchLinkedTopologies = useCallback(async (groupId: string) => {
        try {
            const response = await axios.get<{ topology_id: string; name?: string }[]>(`/api/topology_access/${groupId}`);

            const linkedTopologiesWithNames = await Promise.all(
                response.data.map(async (topology) => {
                    if (!topology.name) {
                        let topologyName = 'not defined';
                        try {
                            const topologyDetails = await axios.get<{ topology_id: string; name: string }>(
                                `/api/tenants/${topology.topology_id}`
                            );
                            topologyName = topologyDetails.data.name;
                        } finally {
                            return { ...topology, name: topologyName };
                        }
                    }
                    return topology;
                })
            );

            const validTopologies = linkedTopologiesWithNames.filter((topology) => topology !== null);
            setLinkedTopologies(validTopologies as TopologyWithPermissions[]);
        } catch (error) {
            console.error('Error fetching linked topologies:', error);
            setError('Error fetching linked topologies');
        }
    }, []);

    const fetchEnvironmentPermissions = useCallback(async (topologyId: string, groupId: string): Promise<EnvironmentWithPermissions[]> => {
        try {
            const response = await axios.get<EnvironmentWithPermissions[]>(
                `/api/environment_access/${topologyId}/${groupId}`
            );
            setLinkedEnvironments(response.data);
            return response.data;
        } catch (error) {
            console.error('Error fetching environment permissions:', error);
            setError('Error fetching environment permissions');
            return [];
        }
    }, []);

    const showModalConfirmation = (action: () => void) => {
        setmodalActionConfirmation(() => action);
        setIsModalConfirmationOpen(true);
    };

    useEffect(() => {
        fetchGroups();
        fetchTopologies();
    }, [fetchGroups, fetchTopologies]);

    const cancelAction = () => {
        setIsModalConfirmationOpen(false);
    };

    const confirmAction = () => {
        modalActionConfirmation();
    };

    const handleSaveGroup = async () => {
        setLoading(true);
        try {
            if (currentGroup.group_id && !addingGroup) {
                await axios.put(`/api/groups/${currentGroup.group_id}`, currentGroup);
            } else {
                await axios.post('/api/groups', currentGroup);
            }
            fetchGroups();
            setShowModal(false);
        } catch (error) {
            console.error('Error saving group:', error);
            setError('Error saving group');
        } finally {
            setLoading(false);
        }
    };

    const handleDeleteGroup = async (group_id: string): Promise<void> => {
        showModalConfirmation(() => {
            try {
                axios.delete(`/api/groups/${group_id}`);
                fetchGroups();
            } catch (error) {
                console.error('Error deleting group:', error);
                setError('Error deleting group');
            } finally {
                setIsModalConfirmationOpen(false);
                fetchGroups();
            }
        })
    };

    const handleEditGroup = (group: Group) => {
        setCurrentGroup(group);
        setAddingGroup(false);
        setShowModal(true);
        fetchLinkedTopologies(group.group_id); // Load linked topologies for the selected group
    };

    const handleLinkTopology = async (topology: Topology) => {
        try {
            await axios.post('/api/topology_access', {
                topology_id: topology.topology_id,
                group_id: currentGroup.group_id,
                allow01: true, // Manage services
                allow02: true, // View services
                allow03: true, // Manage environments
                allow04: false, // Manage ini file
                allow05: false
            });
            // Refresh linked topologies
            fetchLinkedTopologies(currentGroup.group_id);
        } catch (error) {
            console.error('Error linking topology:', error);
            setError('Error linking topology');
        }
    };

    const handleUnlinkTopology = async (topology: Topology) => {
        if (!topology || !topology.topology_id) {
            console.error('Invalid topology object:', topology);
            setError('Invalid topology object');
            return;
        }

        try {
            await axios.delete(`/api/topology_access/${topology.topology_id}/${currentGroup.group_id}`);

            // Refresh linked topologies
            fetchLinkedTopologies(currentGroup.group_id);

            // Reset states related to the unlinked topology
            if (selectedTopology === topology.topology_id) {
                setSelectedTopology('');
                setSelectedEnvironment('');
                setLinkedEnvironments([]);
                setAvailableEnvironments([]);
            }
        } catch (error) {
            console.error('Error unlinking topology:', error);
            setError('Error unlinking topology');
        }
    };

    const handleTopologyPermissionChange = async (topology: TopologyWithPermissions, allow01: boolean) => {
        try {
            await axios.post('/api/topology_access', {
                topology_id: topology.topology_id,
                group_id: currentGroup.group_id,
                allow01: allow01 ? 1 : 0,
                allow02: true,
                allow03: true,
                allow04: false,
                allow05: false
            });
            // Refresh linked topologies
            fetchLinkedTopologies(currentGroup.group_id);
        } catch (error) {
            console.error('Error updating topology permission:', error);
            setError('Error updating topology permission');
        }
    };

    const handleLinkEnvironment = async (environment: string) => {
        try {
            const permissionsAsNumbers = {
                allow01: permissions.allow01 ? 1 : 0,
                allow02: permissions.allow02 ? 1 : 0,
                allow03: permissions.allow03 ? 1 : 0,
                allow04: permissions.allow04 ? 1 : 0,
                allow05: permissions.allow05 ? 1 : 0,
            };

            await axios.post(`/api/environment_access`, {
                topology_id: selectedTopology,
                group_id: currentGroup.group_id,
                environment,
                ...permissionsAsNumbers,
            });

            const envToMove = availableEnvironments.find(env => env.environment === environment);
            if (envToMove) {
                setLinkedEnvironments(prev => [...prev, { ...envToMove, ...permissionsAsNumbers }]);
                setAvailableEnvironments(prev => prev.filter(env => env.environment !== environment));
            }
        } catch (error) {
            console.error('Error linking environment:', error);
            setError('Error linking environment');
        }
    };

    const handleUnlinkEnvironment = async (environment: string) => {
        try {
            await axios.delete(`/api/environment_access/${selectedTopology}/${currentGroup.group_id}/${environment}`);

            const envToMove = linkedEnvironments.find(env => env.environment === environment);
            if (envToMove) {
                const resetEnv = {
                    ...envToMove,
                    allow01: 0,
                    allow02: 0,
                    allow03: 0,
                    allow04: 0,
                    allow05: 0
                };
                setAvailableEnvironments(prev => [...prev, resetEnv]);
                setLinkedEnvironments(prev => prev.filter(env => env.environment !== environment));
            }
        } catch (error) {
            console.error('Error unlinking environment:', error);
            setError('Error unlinking environment');
        }
    };

    const handlePermissionChange = async (environment: string, permission: PermissionKey, value: boolean) => {
        try {
            // Update local permissions state immediately
            setPermissions(prev => ({
                ...prev,
                [permission]: value
            }));

            // Update linkedEnvironments state
            setLinkedEnvironments(prev => prev.map(env => {
                if (env.environment === environment) {
                    return { ...env, [permission]: value ? 1 : 0 };
                }
                return env;
            }));

            // Send to server
            const updatedPermissions = {
                topology_id: selectedTopology,
                group_id: currentGroup.group_id,
                environment: environment,
                allow01: permission === 'allow01' ? (value ? 1 : 0) : permissions.allow01 ? 1 : 0,
                allow02: permission === 'allow02' ? (value ? 1 : 0) : permissions.allow02 ? 1 : 0,
                allow03: permission === 'allow03' ? (value ? 1 : 0) : permissions.allow03 ? 1 : 0,
                allow04: permission === 'allow04' ? (value ? 1 : 0) : permissions.allow04 ? 1 : 0,
                allow05: permission === 'allow05' ? (value ? 1 : 0) : permissions.allow05 ? 1 : 0
            };

            await axios.post('/api/environment_access', updatedPermissions);
        } catch (error) {
            console.error('Error updating permissions:', error);
            setError('Error updating permissions');
            // Rollback the UI state if the server request fails
            const currentEnv = linkedEnvironments.find(env => env.environment === environment);
            if (currentEnv) {
                setPermissions({
                    allow01: currentEnv.allow01 === 1,
                    allow02: currentEnv.allow02 === 1,
                    allow03: currentEnv.allow03 === 1,
                    allow04: currentEnv.allow04 === 1,
                    allow05: currentEnv.allow05 === 1,
                });
            }
        }
    };

    const handleOpenAccessModal = (group: Group) => {
        setCurrentGroup(group);
        setShowAccessModal(true);

        // Reset states
        setSelectedTopology('');
        setLinkedEnvironments([]);
        setLinkedTopologies([]);
        setPermissions({
            allow01: true,
            allow02: true,
            allow03: true,
            allow04: false,
            allow05: false,
        });

        // Fetch data
        fetchLinkedTopologies(group.group_id);
    };

    const handleCloseAccessModal = () => {
        setShowAccessModal(false);
        setCurrentGroup({ group_id: '', name: '', active: 0 }); // Reset current group
        setSelectedTopology(''); // Reset selected topology
        setSelectedEnvironment(''); // Add this line
        setLinkedEnvironments([]); // Clear linked environments
        setLinkedTopologies([]); // Clear linked topologies
    };

    const handleSelectTopology = (topology: Topology) => {
        setSelectedTopology(topology.topology_id);

        // Fetch environments for the selected topology
        fetchEnvironments(topology.topology_id);

        // Fetch permissions for linked environments and update linkedEnvironments
        fetchEnvironmentPermissions(topology.topology_id, currentGroup.group_id).then((response) => {
            setLinkedEnvironments(response);
        });
    };

    const handleSelectEnvironment = async (env: EnvironmentWithPermissions) => {
        setSelectedEnvironment(env.environment);

        // Update permissions based on the selected environment's current state
        const currentEnv = linkedEnvironments.find(e => e.environment === env.environment);
        if (currentEnv) {
            setPermissions({
                allow01: currentEnv.allow01 === 1,
                allow02: currentEnv.allow02 === 1,
                allow03: currentEnv.allow03 === 1,
                allow04: currentEnv.allow04 === 1,
                allow05: currentEnv.allow05 === 1,
            });
        }
    };

    const getPermissionLabel = (permission: PermissionKey): string => {
        switch (permission) {
            case 'allow01': return 'Promove RPO';
            case 'allow02': return 'Aplica Patch';
            case 'allow03': return 'Remover Ambiente';
            case 'allow04': return 'Altera Configuração';
            default: return permission.charAt(0).toUpperCase() + permission.slice(1);
        }
    };

    return (
        <div className="user-management">
            {error && <div className="alert alert-danger">{error}</div>}
            <button className="add-user-button" onClick={() => { setCurrentGroup({ group_id: '', name: '', active: 0 }); setAddingGroup(true); setShowModal(true); }}>Incluir</button>

            {loading ? <div className="user-management" role="status"><span className="sr-only">Loading...</span></div> : (
                <table className="user-table">
                    <thead>
                        <tr>
                            <th>Grupo</th>
                            <th>Nome</th>
                            <th>Ativo</th>
                            <th>Ações</th>
                        </tr>
                    </thead>
                    <tbody>
                        {groups.map(group => (
                            <tr key={group.group_id}>
                                <td>{group.group_id}</td>
                                <td>{group.name}</td>
                                <td>{group.active === 1 ? 'Sim' : 'Não'}</td>
                                <td>
                                    <button className="edit-button" onClick={() => handleEditGroup(group)}>Alterar</button>
                                    <button
                                        className={`access-button ${group.group_id === 'admin' ? 'disabled' : ''}`}
                                        onClick={() => handleOpenAccessModal(group)}
                                        disabled={group.group_id === 'admin'}
                                    >
                                        Acessos
                                    </button>
                                    <button
                                        className={`delete-button ${group.group_id === 'admin' ? 'disabled' : ''}`}
                                        disabled={group.group_id === 'admin'}
                                        onClick={() => handleDeleteGroup(group.group_id)}
                                    >
                                        Excluir
                                    </button>
                                </td>
                            </tr>
                        ))}
                    </tbody>
                </table>
            )}

            {showModal && (
                <div className="Services-overlayViewLog"
                    style={{
                        maxWidth: '100%',  // Reduce to half size
                        height: '100%'
                    }}>
                    <div className="Services-modal" style={{
                        position: 'relative',
                        maxWidth: '30%',  // Reduce to half size
                        display: 'flex',
                        flexDirection: 'column',
                        height: '60%',
                        margin: 'auto',
                    }}>
                        <div className="Services-modalDialog" style={{
                            position: 'relative',
                            maxWidth: '98%',  // Reduce to half size
                            display: 'flex',
                            flexDirection: 'column',
                            height: '100%'
                        }}>
                            <label className="status-dialog">{currentGroup.group_id ? 'Alterando' : 'Incluindo'}</label>
                            <div className="Services-modalBody" style={{ flex: 1 }}>
                                <form>
                                    <div className="form-group">
                                        <label>ID do Gupo</label>
                                        <input disabled={!addingGroup} type="text" className="form-control" value={currentGroup.group_id} onChange={(e) => setCurrentGroup({ ...currentGroup, group_id: e.target.value })} />
                                    </div>
                                    <div className="form-group">
                                        <label>Nome</label>
                                        <input type="text" className="form-control" value={currentGroup.name} onChange={(e) => setCurrentGroup({ ...currentGroup, name: e.target.value })} />
                                    </div>
                                    <label>Ativo</label>
                                    <div className="checkbox-field">
                                        <input type="checkbox" checked={currentGroup.active === 1} onChange={(e) => setCurrentGroup({ ...currentGroup, active: e.target.checked ? 1 : 0 })} />
                                    </div>
                                </form>
                            </div>
                            <div className="modalActions-bottom-right">
                                <button type="button" className="add-user-button" onClick={handleSaveGroup}>Gravar</button>
                                <button type="button" className="add-user-button" onClick={() => setShowModal(false)}>Fechar</button>
                            </div>
                        </div>
                    </div>
                </div>
            )}
            {showAccessModal && (
                <div className="Services-overlayViewLog">
                    <div className="Services-modal" style={{ maxWidth: '90vw', width: '1200px', maxHeight: '90vh' }}>
                        <div className="Services-modalDialog" style={{ position: 'relative', height: '100%' }}>
                            <label>Gerenciar Acessos - {currentGroup.name}</label>
                            <div className="modalActions-top-right">
                                <button type="button" className="add-user-button" onClick={handleCloseAccessModal}>Fechar</button>
                            </div>
                            <div className="Services-modalHeader">
                                <div className="Services-modalBody" style={{ height: 'calc(90vh - 100px)' }}>
                                    <div style={{ display: 'flex', gap: '20px', height: '100%' }}>
                                        {/* Topologies Section */}
                                        <div style={{ width: '480px', height: '100%', border: '1px solid #ccc', borderRadius: '4px', display: 'flex', flexDirection: 'column' }}>
                                            <h4 style={{ padding: '15px', margin: 0, backgroundColor: '#f8f9fa', borderBottom: '1px solid #ccc' }}>Topologias</h4>
                                            <div style={{ padding: '15px', overflowY: 'auto', flex: 1 }}>
                                                <ul className="list-group">
                                                    {Array.from(new Set([...topologies.map(t => t.topology_id)]))
                                                        .map(topologyId => {
                                                            const topology = topologies.find(t => t.topology_id === topologyId);
                                                            if (!topology) return null;

                                                            const linkedTopology = linkedTopologies.find(
                                                                (linked) => String(linked.topology_id) === String(topology.topology_id)
                                                            );
                                                            const isLinked = !!linkedTopology;

                                                            return (
                                                                <li
                                                                    key={topology.topology_id}
                                                                    className="list-group-item"
                                                                    style={{
                                                                        cursor: 'pointer',
                                                                        backgroundColor: selectedTopology === topology.topology_id ? '#e9ecef' : 'transparent'
                                                                    }}
                                                                    onClick={() => isLinked ? handleSelectTopology(topology) : null}
                                                                >
                                                                    <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
                                                                        <span>{topology.name}</span>
                                                                        <div style={{ display: 'flex', gap: '20px', alignItems: 'center' }}>
                                                                            <div style={{ display: 'flex', flexDirection: 'column', alignItems: 'center' }}>
                                                                                <span style={{ fontSize: '0.8em', marginBottom: '2px' }}>Acessa</span>
                                                                                <label className="toggle-switch">
                                                                                    <input
                                                                                        type="checkbox"
                                                                                        checked={isLinked}
                                                                                        onChange={(e) => {
                                                                                            e.stopPropagation();
                                                                                            if (isLinked) {
                                                                                                handleUnlinkTopology(topology);
                                                                                            } else {
                                                                                                handleLinkTopology(topology);
                                                                                            }
                                                                                        }}
                                                                                    />
                                                                                    <span className="toggle-slider"></span>
                                                                                </label>
                                                                            </div>
                                                                            <div style={{ width: '1px', height: '30px', backgroundColor: '#ccc', margin: '0 10px' }}></div>
                                                                            <div style={{ display: 'flex', flexDirection: 'column', alignItems: 'center', opacity: isLinked ? 1 : 0.5 }}>
                                                                                <span style={{ fontSize: '0.8em', marginBottom: '2px' }}>Controla Serviços</span>
                                                                                <label className="toggle-switch">
                                                                                    <input
                                                                                        type="checkbox"
                                                                                        checked={linkedTopology?.allow01 === 1}
                                                                                        disabled={!isLinked}
                                                                                        onChange={(e) => {
                                                                                            e.stopPropagation();
                                                                                            if (isLinked) {
                                                                                                handleTopologyPermissionChange(topology, e.target.checked);
                                                                                            }
                                                                                        }}
                                                                                    />
                                                                                    <span className="toggle-slider"></span>
                                                                                </label>
                                                                            </div>
                                                                        </div>
                                                                    </div>
                                                                </li>
                                                            );
                                                        })}
                                                </ul>
                                            </div>
                                        </div>

                                        {/* Environments Section */}
                                        <div style={{ width: '240px', height: '100%', border: '1px solid #ccc', borderRadius: '4px', display: 'flex', flexDirection: 'column' }}>
                                            <h4 style={{ padding: '15px', margin: 0, backgroundColor: '#f8f9fa', borderBottom: '1px solid #ccc' }}>Ambientes</h4>
                                            <div style={{ padding: '15px', overflowY: 'auto', flex: 1 }}>
                                                {selectedTopology ? (
                                                    <div className="form-group">
                                                        <ul className="list-group">
                                                            {[...availableEnvironments, ...linkedEnvironments]
                                                                .sort((a, b) => a.environment.localeCompare(b.environment))
                                                                .filter((env, index, self) =>
                                                                    index === self.findIndex(e => e.environment === env.environment)
                                                                )
                                                                .map((env) => {
                                                                    const isLinked = linkedEnvironments.some(
                                                                        linked => linked.environment === env.environment
                                                                    );
                                                                    return (
                                                                        <li
                                                                            key={env.environment}
                                                                            className="list-group-item"
                                                                            style={{
                                                                                cursor: 'pointer',
                                                                                backgroundColor: selectedEnvironment === env.environment ? '#e9ecef' : 'transparent'
                                                                            }}
                                                                            onClick={() => isLinked ? handleSelectEnvironment(env) : null}
                                                                        >
                                                                            <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
                                                                                <span>{env.environment}</span>
                                                                                <label className="toggle-switch">
                                                                                    <input
                                                                                        type="checkbox"
                                                                                        checked={isLinked}
                                                                                        onChange={(e) => {
                                                                                            e.stopPropagation();
                                                                                            if (isLinked) {
                                                                                                handleUnlinkEnvironment(env.environment);
                                                                                            } else {
                                                                                                handleLinkEnvironment(env.environment);
                                                                                            }
                                                                                        }}
                                                                                    />
                                                                                    <span className="toggle-slider"></span>
                                                                                </label>
                                                                            </div>
                                                                        </li>
                                                                    );
                                                                })}
                                                        </ul>
                                                    </div>
                                                ) : (
                                                    <p>Selecione uma topologia para ver os ambientes</p>
                                                )}
                                            </div>
                                        </div>

                                        {/* Permissions Section */}
                                        <div style={{ width: '280px', height: '100%', border: '1px solid #ccc', borderRadius: '4px', display: 'flex', flexDirection: 'column' }}>
                                            <h4 style={{ padding: '15px', margin: 0, backgroundColor: '#f8f9fa', borderBottom: '1px solid #ccc' }}>Permissões</h4>
                                            <div style={{ padding: '15px', overflowY: 'auto', flex: 1 }}>
                                                {selectedEnvironment ? (
                                                    <div style={{ display: 'flex', flexDirection: 'column', gap: '10px', alignItems: 'flex-start' }}>
                                                        {(['allow01', 'allow02', 'allow03', 'allow04'] as PermissionKey[]).map((permission) => (
                                                            <div key={permission} style={{ display: 'flex', alignItems: 'left', width: '100%' }}>
                                                                <input
                                                                    type="checkbox"
                                                                    checked={permissions[permission]}
                                                                    onChange={(e) => handlePermissionChange(selectedEnvironment, permission, e.target.checked)}
                                                                    style={{ marginRight: '8px', minWidth: '16px' }}
                                                                />
                                                                <label style={{ fontWeight: 'bold', fontSize: '0.9em', margin: 0 }}>
                                                                    {getPermissionLabel(permission)}
                                                                </label>
                                                            </div>
                                                        ))}
                                                    </div>
                                                ) : (
                                                    <p>Selecione um ambiente para configurar permissões</p>
                                                )}
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            )}
            <ConfirmationModal
                isOpen={isModalConfirmationOpen}
                title="Confirmação Operação?"
                message=""
                onConfirm={confirmAction}
                onCancel={cancelAction}
            />
        </div>
    );
};

export default GroupsManagement;

