import React, { useEffect, useState } from 'react';
import axios from 'axios';
import styles from './DownloadFile.module.css'; // Import the CSS Module

interface DownloadFileProps {
  tenantId: string | null;
}

const getBaseUrl = () => {
  return ''; // Replace with your actual base URL
};

const DownloadFile: React.FC<DownloadFileProps> = ({ tenantId }) => {
  const [fileOptions, setFileOptions] = useState<{ value: string; label: string }[]>([]);
  const [selectedFiles, setSelectedFiles] = useState<Set<string>>(new Set());
  const [fileContent, setFileContent] = useState<string | null>(null);
  const [currentFileName, setCurrentFileName] = useState<string>('');
  const [currentFileId, setCurrentFileId] = useState<string>('');
  const [showFileModal, setShowFileModal] = useState<boolean>(false);
  const [isLoading, setIsLoading] = useState<boolean>(false);
  const [fileTypeState, setFileTypeState] = useState<string>('text');

  useEffect(() => {
    const fetchFiles = async () => {
      if (tenantId) {
        try {
          setIsLoading(true);
          const response = await axios.get(`${getBaseUrl()}/api/list-files/${tenantId}`);
          const files = response.data;
          const options = files.map((file: { id: string; name: string }) => ({
            value: file.id,
            label: file.name,
          }));
          setFileOptions(options);
        } catch (error) {
          console.error('Error fetching files:', error);
        } finally {
          setIsLoading(false);
        }
      }
    };

    fetchFiles();
  }, [tenantId]);

  const handleDownload = async () => {
    if (!currentFileId) {
      alert('No file selected for download.');
      return;
    }

    try {
      setIsLoading(true);
      const downloadResponse = await axios.get(`${getBaseUrl()}/api/download-file/${tenantId}/${currentFileId}`, {
        responseType: 'blob',
      });

      const url = window.URL.createObjectURL(new Blob([downloadResponse.data]));
      const link = document.createElement('a');
      link.href = url;
      link.setAttribute('download', currentFileName);
      document.body.appendChild(link);
      link.click();
      document.body.removeChild(link);
    } catch (error) {
      console.error(`Error downloading file with ID ${currentFileId}:`, error);
    } finally {
      setIsLoading(false);
    }
  };

  const viewFileContent = async (fileId: string, fileName: string) => {
    try {
      setIsLoading(true);
      setCurrentFileId(fileId);
      setCurrentFileName(fileName);

      const extension = fileName.split('.').pop()?.toLowerCase() || '';
      if (['jpg', 'jpeg', 'png', 'gif', 'bmp'].includes(extension)) {
        setFileTypeState('image');
      } else if (['pdf'].includes(extension)) {
        setFileTypeState('pdf');
      } else {
        setFileTypeState('text');
      }

      const response = await axios.get(`${getBaseUrl()}/api/download-file/${tenantId}/${fileId}`, {
        responseType: fileTypeState === 'text' ? 'text' : 'blob',
      });

      if (fileTypeState === 'text') {
        setFileContent(response.data);
      } else {
        const url = window.URL.createObjectURL(new Blob([response.data]));
        setFileContent(url);
      }

      setShowFileModal(true);
    } catch (error) {
      console.error(`Error viewing file with ID ${fileId}:`, error);
      alert('Error loading file content. Please try again.');
    } finally {
      setIsLoading(false);
    }
  };

  const closeFileModal = () => {
    setShowFileModal(false);
    setFileContent(null);
    if (fileTypeState !== 'text' && fileContent) {
      URL.revokeObjectURL(fileContent);
    }
  };

  return (
    <div className={styles.overlay}>
      {isLoading && <div className={styles.loading}>Loading...</div>}

      <table className={styles.table}>
        <thead>
          <tr>
            <th>ID</th>
            <th>Nome</th>
            <th>Ações</th>
          </tr>
        </thead>
        <tbody>
          {fileOptions.map((file) => (
            <tr key={file.value}>
              <td>{file.value}</td>
              <td>{file.label}</td>
              <td>
                <button
                  className={styles.button}
                  onClick={() => viewFileContent(file.value, file.label)}
                >
                  Visualizar
                </button>
                <button
                  className={styles.button}
                  onClick={async () => {
                    try {
                      setIsLoading(true);
                      const downloadResponse = await axios.get(
                        `${getBaseUrl()}/api/download-file/${tenantId}/${file.value}`,
                        { responseType: 'blob' }
                      );

                      const url = window.URL.createObjectURL(new Blob([downloadResponse.data]));
                      const link = document.createElement('a');
                      link.href = url;
                      link.setAttribute('download', file.label);
                      document.body.appendChild(link);
                      link.click();
                      document.body.removeChild(link);
                    } catch (error) {
                      console.error(`Error downloading file with ID ${file.value}:`, error);
                    } finally {
                      setIsLoading(false);
                    }
                  }}
                >
                  Download
                </button>
              </td>
            </tr>
          ))}
        </tbody>
      </table>
      {showFileModal && (
        <div className={styles.overlayViewLog}>
          <div className={styles.modal}>
            <div className={styles.modalDialog}>
              <div className={styles.modalContent}>
                <div className={styles.modalHeader}>
                  <h4>{currentFileName}</h4>
                  <div className={styles.modalActions}>
                    <button
                      className={styles.button}
                      onClick={handleDownload}
                      disabled={isLoading}
                    >
                      Download
                    </button>
                    <button
                      className={styles.button}
                      onClick={closeFileModal}
                    >
                      Fechar
                    </button>
                  </div>
                </div>
                <div className={styles.modalBody}>
                  {fileTypeState === 'text' && (
                    <pre className={styles.fileContent}>{fileContent}</pre>
                  )}
                  {fileTypeState === 'image' && (
                    <img src={fileContent || ''} alt={currentFileName} className={styles.imageContent} />
                  )}
                  {fileTypeState === 'pdf' && (
                    <iframe
                      src={fileContent || ''}
                      title={currentFileName}
                      className={styles.pdfViewer}
                    />
                  )}
                </div>
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default DownloadFile;