import React, { useState, useEffect } from 'react';
import './Config.css';
import axios from 'axios';
import ConfirmationModal from "../ConfirmationModal/ConfirmationModal";

interface Config {
  id: string;
  name: string;
  services: string[];
  services_always_on: string[];
  services_refresh_schedule: string[];
  exclusive_mode_services: string[];
  environments: string[];
  properties: Array<{ environment: string; history: number }>;
  path_to_ini: string[];
  path_to_compiler: string;
  compiler_environment: string;
  path_to_files: Array<{ id: string; name: string; path: string }>;
}

const Config: React.FC = () => {
  const API_URL = '';
  const defaultConfig: Config = {
    id: '',
    name: 'New Configuration',
    services: [],
    services_always_on: [],
    services_refresh_schedule: [],
    exclusive_mode_services: [],
    environments: [],
    properties: [],
    path_to_ini: [],
    path_to_compiler: '',
    compiler_environment: '',
    path_to_files: []
  };

  const [configs, setConfigs] = useState<Config[]>([]);
  const [selectedConfig, setSelectedConfig] = useState<Config | null>(null);
  const [activeSection, setActiveSection] = useState('geral');
  const [newService, setNewService] = useState('');
  const [newAlwaysOnService, setNewAlwaysOnService] = useState('');
  const [newRefreshService, setNewRefreshService] = useState('');
  const [newExclusiveService, setNewExclusiveService] = useState('');
  const [newEnvironment, setNewEnvironment] = useState('');
  const [newIniPath, setNewIniPath] = useState('');
  const [newFilePath, setNewFilePath] = useState({ id: '', name: '', path: '' });
  const [newProperty, setNewProperty] = useState({ environment: '', history: 0 });
  const [isEditing, setIsEditing] = useState(false);
  const [editSuccess, setEditSuccess] = useState<boolean | null>(null);
  const [editMessage, setEditMessage] = useState('');
  const [isNew, setIsNew] = useState(false);
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [modalAction, setModalAction] = useState<() => void>(() => { });

  const openModal = (action: () => void) => {
    setModalAction(() => action);
    setIsModalOpen(true);
  };

  const confirmAction = () => {
    if (modalAction) modalAction();
    setIsModalOpen(false);
  };

  const cancelAction = () => {
    setIsModalOpen(false);
  };

  // Fetch configurations from API
  useEffect(() => {
    const fetchConfigs = async () => {
      try {
        const { data } = await axios.get(`${API_URL}/api/configs`);
        setConfigs(data);
      } catch (error) {
        console.error('Error fetching configs:', error);
      }
    };
    fetchConfigs();
  }, []);

  // Configuration CRUD operations
  const createConfig = async (config: Config) => {
    try {
      setIsEditing(true);
      const { data: newConfig } = await axios.post(`${API_URL}/api/configs`, config);
      setConfigs([...configs, newConfig]);
      setSelectedConfig(newConfig);
      setEditSuccess(true);
      setEditMessage('Configuration created successfully!');
    } catch (error) {
      console.error('Error creating config:', error);
      setEditSuccess(false);
      setEditMessage(
        `Failed to create configuration: ${axios.isAxiosError(error) ? error.message : 'Unknown error'
        }`
      );
    } finally {
      setIsEditing(false);
      setTimeout(() => setEditSuccess(null), 3000);
    }
  };

  const updateConfig = async (config: Config) => {
    try {
      setIsEditing(true);
      await axios.put(`${API_URL}/api/configs/${config.id}`, config);
      setConfigs(configs.map(c => (c.id === config.id ? config : c)));
      setEditSuccess(true);
      setEditMessage('Configuração atualizada com sucesso!');
    } catch (error) {
      console.error('Erro ao atualizar configuração:', error);
      setEditSuccess(false);
      setEditMessage(
        `Falha ao atualizar configuração: ${axios.isAxiosError(error) ? error.message : 'Unknown error'
        }`
      );
    } finally {
      setIsEditing(false);
      setTimeout(() => setEditSuccess(null), 3000);
    }
  };

  const deleteConfig = async (id: string) => {

    try {
      setIsEditing(true);
      await axios.delete(`${API_URL}/api/configs/${id}`);
      setConfigs(configs.filter(c => c.id !== id));
      if (selectedConfig?.id === id) setSelectedConfig(null);
      setEditSuccess(true);
      setEditMessage('Configuration deleted successfully!');
    } catch (error) {
      console.error('Error deleting config:', error);
      setEditSuccess(false);
      setEditMessage(
        `Failed to delete configuration: ${axios.isAxiosError(error) ? error.message : 'Unknown error'
        }`
      );
    } finally {
      setIsEditing(false);
      setTimeout(() => setEditSuccess(null), 3000);
    }
  };  // Handle field updates for any config property
  const handleConfigUpdate = <K extends keyof Config>(field: K, value: Config[K]) => {
    if (!selectedConfig) return;
    const updated = { ...selectedConfig, [field]: value };
    setSelectedConfig(updated);
  };

  // Service management
  const handleServiceUpdate = (field: keyof Config, value: string[]) => {
    if (!selectedConfig) return;
    handleConfigUpdate(field, value);
  };

  // Environment management
  const handleEnvironmentUpdate = (environments: string[]) => {
    if (!selectedConfig) return;
    handleConfigUpdate('environments', environments);
  };

  // Add a new file path
  const handleAddFilePath = () => {
    if (!selectedConfig || !newFilePath.name || !newFilePath.path) return;

    // Prevent duplicate paths
    const exists = selectedConfig.path_to_files.some(
      file => file.path === newFilePath.path
    );
    if (exists) {
      setEditSuccess(false);
      setEditMessage('Este caminho já foi adicionado.');
      setTimeout(() => setEditSuccess(null), 3000);
      return;
    }

    const newId = Date.now().toString();
    const updatedFilePaths = [
      ...selectedConfig.path_to_files,
      { ...newFilePath, id: newId }
    ];

    handleConfigUpdate('path_to_files', updatedFilePaths);
    setNewFilePath({ id: '', name: '', path: '' });
  };

  // Remove a file path
  const handleRemoveFilePath = (id: string) => {
    if (!selectedConfig) return;

    const updatedFilePaths = selectedConfig.path_to_files.filter(
      file => file.id !== id
    );

    handleConfigUpdate('path_to_files', updatedFilePaths);
  };

  // Add a new property
  const handleAddProperty = () => {
    if (!selectedConfig || !newProperty.environment) return;

    const updatedProperties = Array.isArray(selectedConfig.properties)
      ? [...selectedConfig.properties, { ...newProperty }]
      : [{ ...newProperty }];

    handleConfigUpdate('properties', updatedProperties);
    setNewProperty({ environment: '', history: 0 });
  };

  // Remove a property
  const handleRemoveProperty = (index: number) => {
    if (!selectedConfig || !Array.isArray(selectedConfig.properties)) return;

    const updatedProperties = selectedConfig.properties.filter((_, i) => i !== index);
    handleConfigUpdate('properties', updatedProperties);
  };

  // Save changes
  const saveChanges = () => {
    if (!selectedConfig) return;

    // Check if ID already exists (only for new configurations)
    if (isNew && configs.some(config => config.id === selectedConfig.id)) {
      setEditSuccess(false);
      setEditMessage('Este ID já existe. Por favor, escolha um ID único.');
      setTimeout(() => setEditSuccess(null), 3000);
      return;
    }

    if (isNew) {
      createConfig(selectedConfig); // Use createConfig for new configurations
    } else {
      updateConfig(selectedConfig); // Use updateConfig for existing configurations
    }
  };

  // Render configuration list
  const renderConfigList = () => (
    <div className="Config-config-list">
      <button
        className="Config-btn-new"
        onClick={() => {
          setSelectedConfig({ ...defaultConfig });
          setActiveSection('geral');
          setIsNew(true); // Set isNew to true for new configurations
        }}
      >
        + Nova Topologia
      </button>
      {configs && configs.length > 0 ? (
        configs.map(config => (
          <div
            key={config.id}
            className={`Config-config-item ${selectedConfig?.id === config.id ? 'active' : ''}`}
            onClick={() => {
              setSelectedConfig({ ...config });
              setActiveSection('geral');
              setIsNew(false); // Set isNew to false for existing configurations
            }}
          >
            <label>{config.name}</label>
            <div className="Config-config-meta">
              <div><span>ID: {config.id}</span></div>
              <div><span>Serviços: {config.services?.length || 0}</span></div>
              <div><span>Environments: {config.environments?.length || 0}</span></div>
            </div>
            <div className="Config-config-actions">
              <button
                className="Config-btn-edit"
                onClick={(e) => {
                  e.stopPropagation();
                  setIsNew(false);
                  setSelectedConfig({ ...config });
                  setActiveSection('geral');
                }}
              >
                Editar
              </button>
              <button
                className="Config-btn-delete"
                onClick={(e) => {
                  e.stopPropagation();
                  openModal(() => deleteConfig(config.id));
                }}
              >
                Apagar
              </button>
            </div>
          </div>

        ))
      ) : (
        <p>Nenhuma topologia encontrada.</p>
      )}
      <ConfirmationModal
        isOpen={isModalOpen}
        title="Confirmação Operação?"
        message=""
        onConfirm={confirmAction}
        onCancel={cancelAction}
      />

    </div>
  );

  // Render editor sections
  const renderGeneralSection = () => {
    if (!selectedConfig) return null;

    return (
      <div className="Config-section-content">
        {isNew && (
          <div className="Config-form-group">
            <label>ID da Topologia</label>
            <input
              type="number"
              placeholder="Digite o ID da topologia"
              value={selectedConfig.id}
              readOnly={!isNew}
              onChange={(e) => handleConfigUpdate('id', e.target.value)}
            />
          </div>
        )}
        <div className="Config-form-group">
          <label>Nome da Topologia</label>
          <input
            value={selectedConfig.name}
            onChange={(e) => handleConfigUpdate('name', e.target.value)}
          />
        </div>
        <div className="Config-form-group">
          <label>Caminho do Compilador</label>
          <input
            placeholder="Path to compiler"
            value={selectedConfig.path_to_compiler}
            onChange={(e) => handleConfigUpdate('path_to_compiler', e.target.value)}
          />
        </div>
        <div className="Config-form-group">
          <label>Environment do Compilador</label>
          <input
            placeholder="Compiler environment"
            value={selectedConfig.compiler_environment}
            onChange={(e) => handleConfigUpdate('compiler_environment', e.target.value)}
          />
        </div>
      </div>
    );
  };

  const renderServicesSection = () => {
    if (!selectedConfig) return null;

    return (
      <div className="Config-section-content">
        {/* Regular Services */}
        <div className="Config-service-section">
          <label>Serviços</label>
          <div className="Config-service-controls">
            <input
              value={newService}
              onChange={(e) => setNewService(e.target.value)}
              placeholder="Novo serviço"
            />
            <button className="Config-btn-save" onClick={() => {
              if (newService.trim()) {
                handleServiceUpdate('services', [...(selectedConfig.services || []), newService]);
                setNewService('');
              }
            }}>
              Adicionar
            </button>
          </div>
          <div className="Config-service-list">
            {(!selectedConfig.services || selectedConfig.services.length === 0) && <p>No services added yet.</p>}
            {selectedConfig.services && selectedConfig.services.map(service => (
              <div key={service} className="Config-service-item">
                {service}
                <button onClick={() =>
                  handleServiceUpdate('services', selectedConfig.services.filter(s => s !== service))
                }>
                  ×
                </button>
              </div>
            ))}
          </div>
        </div>

        {/* Always On Services */}
        <div className="Config-service-section">
          <label>Serviços Sempre Ativos</label>
          <div className="Config-service-controls">
            <input
              value={newAlwaysOnService}
              onChange={(e) => setNewAlwaysOnService(e.target.value)}
              placeholder="Novo serviço"
            />
            <button className="Config-btn-save" onClick={() => {
              if (newAlwaysOnService.trim()) {
                handleServiceUpdate('services_always_on', [...(selectedConfig.services_always_on || []), newAlwaysOnService]);
                setNewAlwaysOnService('');
              }
            }}>
              Adicionar
            </button>
          </div>
          <div className="Config-service-list">
            {selectedConfig.services_always_on && selectedConfig.services_always_on.map(service => (
              <div key={service} className="Config-service-item">
                {service}
                <button onClick={() =>
                  handleServiceUpdate('services_always_on', selectedConfig.services_always_on.filter(s => s !== service))
                }>
                  ×
                </button>
              </div>
            ))}
          </div>
        </div>

        {/* Refresh Schedule Services */}
        <div className="Config-service-section">
          <label>Serviços com Refresh</label>
          <div className="Config-service-controls">
            <input
              value={newRefreshService}
              onChange={(e) => setNewRefreshService(e.target.value)}
              placeholder="Novo serviço"
            />
            <button className="Config-btn-save" onClick={() => {
              if (newRefreshService.trim()) {
                handleServiceUpdate('services_refresh_schedule', [...(selectedConfig.services_refresh_schedule || []), newRefreshService]);
                setNewRefreshService('');
              }
            }}>
              Adicionar
            </button>
          </div>
          <div className="Config-service-list">
            {selectedConfig.services_refresh_schedule && selectedConfig.services_refresh_schedule.map(service => (
              <div key={service} className="Config-service-item">
                {service}
                <button onClick={() =>
                  handleServiceUpdate('services_refresh_schedule', selectedConfig.services_refresh_schedule.filter(s => s !== service))
                }>
                  ×
                </button>
              </div>
            ))}
          </div>
        </div>

        {/* Exclusive Mode Services */}
        <div className="Config-service-section">
          <label>Serviços do Modo Exclusivo</label>
          <div className="Config-service-controls">
            <input
              value={newExclusiveService}
              onChange={(e) => setNewExclusiveService(e.target.value)}
              placeholder="Novo serviço"
            />
            <button className="Config-btn-save" onClick={() => {
              if (newExclusiveService.trim()) {
                handleServiceUpdate('exclusive_mode_services', [...(selectedConfig.exclusive_mode_services || []), newExclusiveService]);
                setNewExclusiveService('');
              }
            }}>
              Adicionar
            </button>
          </div>
          <div className="Config-service-list">
            {selectedConfig.exclusive_mode_services && selectedConfig.exclusive_mode_services.map(service => (
              <div key={service} className="Config-service-item">
                {service}
                <button onClick={() =>
                  handleServiceUpdate('exclusive_mode_services', selectedConfig.exclusive_mode_services.filter(s => s !== service))
                }>
                  ×
                </button>
              </div>
            ))}
          </div>
        </div>
      </div>
    );
  };

  const renderEnvironmentsSection = () => {
    if (!selectedConfig) {
      return null;
    }

    const environments = selectedConfig.environments || [];
    const properties = selectedConfig.properties || [];

    return (
      <div className="Config-section-content">
        <div className="Config-environment-section">
          <label>Environments</label>
          <div className="Config-environment-controls">
            <input
              value={newEnvironment}
              onChange={(e) => setNewEnvironment(e.target.value)}
              placeholder="Novo environment"
            />
            <button className="Config-btn-save" onClick={() => {
              if (newEnvironment.trim()) {
                handleEnvironmentUpdate([...environments, newEnvironment]);
                setNewEnvironment('');
              }
            }}>
              Adicionar
            </button>
          </div>
          <div className="Config-environment-list">
            {environments.length === 0 && <p>No environments added yet.</p>}
            {environments.map(env => (
              <div key={env} className="Config-environment-item">
                {env}
                <button onClick={() =>
                  handleEnvironmentUpdate(environments.filter(e => e !== env))
                }>
                  ×
                </button>
              </div>
            ))}
          </div>
        </div>

        <div className="Config-properties-section">
          <label>Histórico de RPO</label>
          <div className="Config-property-controls">
            <select
              value={newProperty.environment}
              onChange={(e) => setNewProperty({ ...newProperty, environment: e.target.value })}
            >
              <option value="">Selecione Environment</option>
              {environments.map(env => (
                <option key={env} value={env}>{env}</option>
              ))}
            </select>
            <input
              type="number"
              placeholder="Cópias"
              value={newProperty.history || ''}
              onChange={(e) => setNewProperty({ ...newProperty, history: parseInt(e.target.value) || 0 })}
            />
            <button className="Config-btn-save" onClick={handleAddProperty} disabled={!newProperty.environment}>
              Adicionar
            </button>
          </div>
          <div className="Config-property-list">
            {properties.map((prop, index) => (
              <div key={index} className="Config-property-item">
                <div><span>Environment: {prop.environment}</span></div>
                <div><span>Histórico: {prop.history} cópias</span></div>
                <button onClick={() => handleRemoveProperty(index)}>×</button>
              </div>
            ))}
          </div>
        </div>
      </div>
    );
  };


  const renderPathsSection = () => {
    if (!selectedConfig) return null;

    return (
      <div className="Config-section-content">
        <div className="Config-ini-paths-section">
          <label>Caminho dos arquivos .ini</label>
          <div className="Config-path-controls">
            <input
              value={newIniPath}
              onChange={(e) => setNewIniPath(e.target.value)}
              placeholder="Caminho para arquivo INI"
            />
            <button
              className="Config-btn-save"
              onClick={() => {
                if (newIniPath.trim()) {
                  if (selectedConfig.path_to_ini.includes(newIniPath)) {
                    setEditSuccess(false);
                    setEditMessage('Este caminho já foi adicionado.');
                    setTimeout(() => setEditSuccess(null), 3000);
                    return;
                  }
                  handleConfigUpdate('path_to_ini', [...(selectedConfig.path_to_ini || []), newIniPath]);
                  setNewIniPath('');
                }
              }}
            >
              Adicionar
            </button>
          </div>
          <div className="Config-path-list">
            {(selectedConfig.path_to_ini && selectedConfig.path_to_ini.length > 0) ? (
              selectedConfig.path_to_ini.map((path, index) => (
                <div key={index} className="Config-path-item">
                  {path}
                  <button onClick={() =>
                    handleConfigUpdate('path_to_ini', selectedConfig.path_to_ini.filter((_, i) => i !== index))
                  }>
                    ×
                  </button>
                </div>
              ))
            ) : (
              <p>Sem arquivos.</p>
            )}
          </div>
        </div>

        <div className="Config-file-paths-section">
          <label>Artefatos para Download</label>
          <div className="Config-file-path-controls">
            <input
              placeholder="Nome"
              value={newFilePath.name}
              onChange={(e) => setNewFilePath({ ...newFilePath, name: e.target.value })}
            />
            <input
              placeholder="Caminho"
              value={newFilePath.path}
              onChange={(e) => setNewFilePath({ ...newFilePath, path: e.target.value })}
            />
            <button className="Config-btn-save" onClick={handleAddFilePath} disabled={!newFilePath.name || !newFilePath.path}>
              Adicionar
            </button>
          </div>
          <div className="Config-file-path-list">
            {selectedConfig.path_to_files && selectedConfig.path_to_files.map(file => (
              <div key={file.id} className="Config-file-path-item">
                <span>Name: {file.name}</span>
                <span>Path: {file.path}</span>
                <button onClick={() => handleRemoveFilePath(file.id)}>×</button>
              </div>
            ))}
          </div>
        </div>
      </div>
    );
  };

  return (
    <div className="Config-config-manager">
      {renderConfigList()}

      {selectedConfig && (
        <div className="Config-config-editor">
          <label>{selectedConfig.id ? `Editando: ${selectedConfig.name}` : 'Nova Topologia'}</label>

          {editSuccess !== null && (
            <div className={`Config-notification ${editSuccess ? 'success' : 'error'}`}>
              {editMessage}
            </div>
          )}

          <div className="Config-section-tabs">
            {['geral', 'services', 'environments', 'paths'].map(section => (
              <button
                key={section}
                className={activeSection === section ? 'active' : ''}
                onClick={() => setActiveSection(section)}
              >
                {section.charAt(0).toUpperCase() + section.slice(1)}
              </button>
            ))}
          </div>

          {activeSection === 'geral' && renderGeneralSection()}
          {activeSection === 'services' && renderServicesSection()}
          {activeSection === 'environments' && renderEnvironmentsSection()}
          {activeSection === 'paths' && renderPathsSection()}

          <div className="Config-config-actions">
            <button
              className="Config-btn-cancel"
              onClick={() => setSelectedConfig(null)}
            >
              Cancelar
            </button>
            <button
              className="Config-btn-save"
              onClick={saveChanges}
              disabled={isEditing}
            >
              {isEditing ? 'Salvando...' : (selectedConfig.id ? 'Salvar Alterações' : 'Criar Configuração')}
            </button>
          </div>
        </div>
      )}
    </div>
  );
};

export default Config;
