import React, { useEffect, useState } from 'react';
import axios from 'axios';
import './SideBar.css';
import logo from '../../assets/images/logo.png'; // Import the logo image
import packageJson from '../../../package.json'; // Adjust the path as necessary
const appVersion = packageJson.version;

interface SidebarProps {
  setActivePage: React.Dispatch<React.SetStateAction<string>>;
  setSelectedTenant: React.Dispatch<React.SetStateAction<{ id: string; name: string } | null>>;
  nameSelectedTenant: React.Dispatch<React.SetStateAction<string | null>>;
  username: string;
}

interface Tenant {
  id: string;
  name: string;
  isAllowed?: boolean;
}

const Sidebar: React.FC<SidebarProps> = ({ setActivePage, setSelectedTenant, nameSelectedTenant, username }) => {
  const [tenants, setTenants] = useState<Tenant[]>([]); // State to hold tenants data
  const [error, setError] = useState<string | null>(null);
  const [loading, setLoading] = useState<boolean>(true); // Added loading state
  const [userName, setUserName] = useState<string | null>(null); // State for the username
  const [userGroup, setUserGroup] = useState<string | null>(null);
  const isAdmin = username.toLowerCase() === 'admin';

  // Fetch user info from the /userinfo endpoint using username as a URL parameter
  useEffect(() => {
    const fetchUserInfo = async () => {
      const username = localStorage.getItem('username'); // Get the username from localStorage
      const token = localStorage.getItem('access_token'); // Get the token from localStorage

      if (!username || !token) {
        setError('No username or token found. Please log in.');
        return;
      }

      try {
        const response = await axios.get(`/api/userinfo/${username}`, {
        });

        setUserName(response.data.name); // Assuming the API returns user info as { name: 'User Name' }
      } catch (error) {
        setError('Failed to fetch user info');
        console.error('Error fetching user info:', error);
      }
    };

    fetchUserInfo();
  }, []);

  const checkTenantPermissions = async (tenants: Tenant[]) => {
    const username = localStorage.getItem('username');
    if (!username) return tenants;

    try {
      // First get user group
      const groupResponse = await axios.get(`/api/users/${username}`);
      const group = groupResponse.data.role;
      setUserGroup(group);

      // If admin, all tenants are allowed
      if (group === 'admin') {
        return tenants.map(tenant => ({ ...tenant, isAllowed: true }));
      }

      // Check permissions for each tenant
      const tenantsWithPermissions = await Promise.all(
        tenants.map(async (tenant) => {
          try {
            const response = await axios.get(`/api/topology_access/${tenant.id}/${group}`);
            // If we get any response, the topology is allowed
            return {
              ...tenant,
              isAllowed: response.data.length > 0
            };
          } catch (error) {
            console.error(`Error checking permissions for tenant ${tenant.id}:`, error);
            return { ...tenant, isAllowed: false };
          }
        })
      );

      return tenantsWithPermissions;
    } catch (error) {
      console.error('Error checking tenant permissions:', error);
      return tenants.map(tenant => ({ ...tenant, isAllowed: false }));
    }
  };

  // Fetch tenants from the API
  useEffect(() => {
    const fetchTenants = async () => {
      try {
        const token = localStorage.getItem('access_token'); // Get the token from localStorage

        if (!token) {
          setError('No token found. Please log in.');
          setLoading(false);
          return;
        }

        const response = await axios.get('/api/get-tenants', {
          headers: {
            Authorization: `Bearer ${token}`, // Pass token for authentication
          },
        });

        const tenantsWithPermissions = await checkTenantPermissions(response.data.tenants);
        setTenants(tenantsWithPermissions);
        setLoading(false);
      } catch (error) {
        setError('Failed to fetch tenants');
        console.error('Error fetching tenants:', error);
        setLoading(false);
      }
    };

    fetchTenants();
  }, []);

  // Handle logout

  return (
    <div className="sidebar">
      <ul className="sidebar-menu">
        <li className="sidebar-menu-item">
          <span><b>Opções</b></span>
          <hr />
          <ul className="sidebar-submenu">
            <li className="sidebar-submenu-item" onClick={() => setActivePage('monitor')}><b>Home</b></li>
            <li className="sidebar-submenu-item" onClick={() => setActivePage('profile')}>Conta</li>
            <li className="sidebar-submenu-item" onClick={() => setActivePage('forum')}>Forum</li>

            {isAdmin && (
              <li
                className="sidebar-submenu-item"
                onClick={() => setActivePage('users')}
              >
                Usuários
              </li>
            )}

            {isAdmin && (
              <li
                className="sidebar-submenu-item"
                onClick={() => setActivePage('groups')}
              >
                Grupos
              </li>
            )}

            {isAdmin && (
              <li
                className="sidebar-submenu-item"
                onClick={() => setActivePage('config')}
              >
                Configuração
              </li>
            )}

            <li className="sidebar-submenu-item" onClick={() => setActivePage('log')}>Log de Ações</li>
            <b>Topologias</b>
            {loading ? (
              <li className="sidebar-submenu-item">Loading tenants...</li>
            ) : tenants.length > 0 ? (
              tenants.map((tenant) => (
                <li
                  key={tenant.id}
                  className={`sidebar-submenu-item ${!tenant.isAllowed ? 'dimmed' : ''}`}
                  onClick={() => {
                    if (tenant.isAllowed) {
                      setSelectedTenant({ id: tenant.id, name: tenant.name });
                      setActivePage('services');
                    }
                  }}
                  style={{ cursor: tenant.isAllowed ? 'pointer' : 'not-allowed' }}
                >
                  - {tenant.name}
                </li>
              ))
            ) : (
              <li className="sidebar-submenu-item">No tenants available</li>
            )}
          </ul>
        </li>
      </ul>
      {error && <p className="sidebar-error">{error}</p>}
      <div className="sidebar-logo-container">
        <img src={logo} className="sidebar-logo" alt="Logo" />
        <p>DeskFlow  v.{appVersion}</p>
      </div>
    </div>
  );
};

export default Sidebar;
