import React, { useState, useCallback, useEffect } from 'react';
import { useForm } from 'react-hook-form';
import axios from 'axios';
import styles from './EnvironmentCRUD.module.css';
import ConfirmationModal from '../ConfirmationModal/ConfirmationModal';
import { logParams } from '../Services/Services';

interface Tenant {
    id: string;
    name: string;
}

interface EnvironmentCRUDProps {
    tenants: Tenant[];
    onClose: () => void;
}

interface FormData {
    tenant: string;
    origin_environment: string;
    destination_environment: string;
}

const BASE_URL = ''; // Replace with your actual base URL

const EnvironmentCRUD: React.FC<EnvironmentCRUDProps> = ({ tenants, onClose }) => {
    const { register, handleSubmit, watch, formState: { errors }, reset } = useForm<FormData>();
    const [successMessage, setSuccessMessage] = useState<string | null>(null);
    const [errorMessage, setErrorMessage] = useState<string | null>(null);
    const [tenantEnvironments, setTenantEnvironments] = useState<string[]>([]);
    const [isModalOpen, setIsModalOpen] = useState(false);
    const [formData, setFormData] = useState<FormData | null>(null);

    const selectedTenant = watch('tenant');

    const createLogEntry = async (params: logParams) => {
        try {
            const originTenantName = tenants.find(tenant => tenant.id === params.origin_tenant)?.name || '';
            const destinationTenantName = tenants.find(tenant => tenant.id === params.destination_tenant)?.name || '';
            const now = new Date();
            const logEntry = {
                date: now.toISOString().split('T')[0],
                time: now.toTimeString().split(' ')[0],
                user: localStorage.getItem('username') || 'unknown',
                tenant: originTenantName,
                service: params.serviceName,
                action: params.action,
                origin: params.origin,
                origin_tenant: originTenantName,
                destination_tenant: destinationTenantName || '',
                destination: params.destination || '',
                result: 'In Progress',
                log_result: params.log_result
            };
            const response = await axios.post(`${BASE_URL}/api/logs`, logEntry);
            return response.data.id;
        } catch (error) {
            console.error('Error creating log entry:', error);
            return null;
        }
    };

    const updateLogEntry = async (logId: number, result: string, full_log?: string) => {
        try {
            const now = new Date();
            await axios.put(`${BASE_URL}/api/logs/${logId}`, {
                finish_date: now.toISOString().split('T')[0],
                finish_time: now.toLocaleTimeString('en-US', { hour12: false }),
                result: result,
                log_result: full_log
            });
            if (result === 'Failed') {
                setErrorMessage(`Erro: ${full_log}`);
                setTimeout(() => setErrorMessage(''), 3000); // Hide after 3 seconds
                }
        } catch (error) {
            console.error('Error updating log entry:', error);
        }
    };

    const fetchEnvironmentsForTenant = useCallback(async (tenantId: string) => {
        if (!tenantId) return;
        try {
            const response = await axios.get<{ environments: string[] }>(
                `${BASE_URL}/api/get-environments/${tenantId}`
            );
            setTenantEnvironments(response.data.environments || []);
        } catch (error) {
            console.error('Error fetching environments:', error);
        }
    }, []);

    useEffect(() => {
        if (selectedTenant) {
            fetchEnvironmentsForTenant(selectedTenant);
        }
    }, [selectedTenant, fetchEnvironmentsForTenant]);

    const onSubmit = async (data: FormData) => {
        setSuccessMessage(null);
        setErrorMessage(null);

        const logId = await createLogEntry({
            serviceName: 'Add Environment',
            action: 'Creation',
            origin: data.origin_environment,
            destination: data.destination_environment,
            origin_tenant: data.tenant,
            destination_tenant: data.tenant,
        });

        try {
            const response = await axios.post(`${BASE_URL}/api/add-environment`, {
                tenantId: data.tenant,
                origin_environment: data.origin_environment,
                destination_environment: data.destination_environment,
            });

            if (logId) await updateLogEntry(logId, 'Success', response.data.message);

            setSuccessMessage('Ambiente criado com sucesso!');
            reset();
            if (selectedTenant) fetchEnvironmentsForTenant(selectedTenant);
        } catch (error) {
            if (logId) {
                const errorMessage = axios.isAxiosError(error) && error.response?.data?.error ? error.response.data.error : 'Unknown error';
                await updateLogEntry(logId, 'Failed', errorMessage);
            }
            // setErrorMessage('Falha na criação do ambiente. Veja o log.');
        }
    };

    const handleFormSubmit = (data: FormData) => {
        setFormData(data);
        setIsModalOpen(true);
    };

    const confirmAction = async () => {
        if (formData) {
            await onSubmit(formData);
        }
        setIsModalOpen(false);
    };

    const cancelAction = () => {
        setIsModalOpen(false);
    };

    return (
        <>
            <div className={styles.overlay}>
                <div className={styles.environmentCRUD}>
                    <form onSubmit={handleSubmit(handleFormSubmit)} className={styles.form}>
                        <div>
                            <label><b>Topologia</b></label>
                            <select {...register('tenant', { required: true })}>
                                <option value="">Selecione a Topologia</option>
                                {tenants.map((tenant) => (
                                    <option key={tenant.id} value={tenant.id}>
                                        {tenant.name}
                                    </option>
                                ))}
                            </select>
                            {errors.tenant && <span>Campo requerido</span>}
                        </div>
                        <div>
                            <label><b>Ambiente Base</b></label>
                            <select {...register('origin_environment', { required: true })}>
                                <option value="">Ambiente base</option>
                                {tenantEnvironments.map((env, index) => (
                                    <option key={index} value={env}>
                                        {env}
                                    </option>
                                ))}
                            </select>
                            {errors.origin_environment && <span>Campo requerido</span>}
                        </div>
                        <div>
                            <label><b>Novo Ambiente</b></label>
                            <input
                                type="text"
                                {...register('destination_environment', { required: true })}
                                placeholder="Ambiente de destino"
                            />
                            {errors.destination_environment && <span>Campo requerido</span>}
                        </div>
                        <div className={styles.buttonContainer}>
                            <button type="submit">Adicionar</button>
                            <button type="button" onClick={onClose}>Fechar</button>
                        </div>
                    </form>
                    {successMessage && <div className={styles.successballoon}>{successMessage}</div>}
                    {errorMessage && <div className={styles.failedballoon}>{errorMessage}</div>}
                </div>
            </div>
            <ConfirmationModal
                isOpen={isModalOpen}
                title="Confirmação Operação"
                message="Você confirma as alterações?"
                onConfirm={confirmAction}
                onCancel={cancelAction}
            />
        </>
    );
};

export default EnvironmentCRUD;
