import React, { useEffect, useState } from 'react';
import axios from 'axios';
import './Log.css';
import socket, { Message } from "../.././socket";

interface LogEntry {
  id: number;
  date: string;
  time: string;
  finish_date: string;
  finish_time: string;
  user: string;
  tenant: string;
  service: string;
  action: string;
  origin: string;
  origin_tenant: string;
  destination: string;
  destination_tenant: string;
  result: string;
  log_result: string;
}

const Log: React.FC = () => {
  const [logs, setLogs] = useState<LogEntry[]>([]);
  const [currentPage, setCurrentPage] = useState(1);
  const [totalPages, setTotalPages] = useState(1);
  const [rowsPerPage, setRowsPerPage] = useState(() => {
    return 16;
  });
  const username = localStorage.getItem('username') || 'unknown';
  const [showFilters, setShowFilters] = useState(false);

  useEffect(() => {

    // Listen for incoming messages from the server
    const handleMessage = (message: Message) => {
      if (message.data.user_id && message.data.user_id !== username) {
        handleRefresh();
      }
    };

    socket.on("alert", handleMessage);

    // Clean up listeners on unmount
    return () => {
      socket.off("connect");
      socket.off("connect_error");
    };
  }, []);

  useEffect(() => {
    const calculateRowsPerPage = () => {
      // const tableHeight = window.innerHeight - 100; // Adjust based on your layout
      // const rowHeight = 30; // Approximate height of each row
      // const rows = Math.floor(tableHeight / rowHeight);
      setRowsPerPage(16);
    };

    calculateRowsPerPage();
    window.addEventListener('resize', calculateRowsPerPage);

    return () => {
      window.removeEventListener('resize', calculateRowsPerPage);
    };
  }, []);

  const [filters, setFilters] = useState({
    result: '',
    date: '',
    time: '',
    user: '',
    tenant: '',
    service: '',
    action: '',
    origin_tenant: '',
    origin: '',
    destination_tenant: '',
    destination: ''
  });

  const handleFilterChange = (e: React.ChangeEvent<HTMLInputElement>, field: string) => {
    setFilters({
      ...filters,
      [field]: e.target.value
    });
  };

  const fetchLogs = async (page: number, filters: any) => {
    try {
      const response = await axios.get<{ logs: LogEntry[], totalPages: number }>(`/api/logs`, {
        params: {
          page,
          per_page: rowsPerPage,
          ...filters
        }
      });
      setLogs(response.data.logs);
      setTotalPages(response.data.totalPages);
    } catch (error) {
      console.error('Error fetching logs:', error);
    }
  };

  useEffect(() => {
    fetchLogs(currentPage, filters);
  }, [currentPage, rowsPerPage, filters]);

  const getStatusColor = (result: string) => {
    if (result.toLowerCase().includes('success')) return 'green';
    if (result.toLowerCase().includes('in progress')) return 'orange';
    if (result.toLowerCase().includes('error')) return 'red';
    if (result.toLowerCase().includes('failed')) return 'red';
    return 'grey';
  };

  const handlePreviousPage = () => {
    if (currentPage > 1) {
      setCurrentPage(currentPage - 1);
    }
  };

  const handleNextPage = () => {
    if (currentPage < totalPages) {
      setCurrentPage(currentPage + 1);
    }
  };

  const handleRefresh = () => {
    fetchLogs(currentPage, filters);
  };

  return (
    <div className="log-container">
      <button className="log-button" onClick={() => setShowFilters(!showFilters)}>
        {showFilters ? 'Ocultar Filtros' : 'Mostrar Filtros'}
      </button>
      <button className="log-button" onClick={handleRefresh}>
        Atualizar
      </button>
      <table className="log-table">
        <thead>
          <tr>
            <th>ID</th>
            <th>Resultado</th>
            <th>Dt Inicio</th>
            <th>Hr Inicio</th>
            <th>Dt Fim</th>
            <th>Hr Fim</th>
            <th>Usuário</th>
            <th>Topologia da Ação</th>
            <th>Serviço</th>
            <th>Ação</th>
            <th>Top.Origem / Patch</th>
            <th>Env.Origem</th>
            <th>Top.Destino</th>
            <th>Env.Destino</th>
            <th>Mensagem</th>
          </tr>
          {showFilters && (
            <tr>
              <th></th>
              <th><input type="text" value={filters.result} onChange={(e) => handleFilterChange(e, 'result')} /></th>
              <th><input type="text" value={filters.date} onChange={(e) => handleFilterChange(e, 'date')} /></th>
              <th><input type="text" value={filters.time} onChange={(e) => handleFilterChange(e, 'time')} /></th>
              <th></th>
              <th></th>
              <th><input type="text" value={filters.user} onChange={(e) => handleFilterChange(e, 'user')} /></th>
              <th><input type="text" value={filters.tenant} onChange={(e) => handleFilterChange(e, 'tenant')} /></th>
              <th><input type="text" value={filters.service} onChange={(e) => handleFilterChange(e, 'service')} /></th>
              <th><input type="text" value={filters.action} onChange={(e) => handleFilterChange(e, 'action')} /></th>
              <th><input type="text" value={filters.origin_tenant} onChange={(e) => handleFilterChange(e, 'origin_tenant')} /></th>
              <th><input type="text" value={filters.origin} onChange={(e) => handleFilterChange(e, 'origin')} /></th>
              <th><input type="text" value={filters.destination_tenant} onChange={(e) => handleFilterChange(e, 'destination_tenant')} /></th>
              <th><input type="text" value={filters.destination} onChange={(e) => handleFilterChange(e, 'destination')} /></th>
              <th></th>
            </tr>
          )}
        </thead>
        <tbody>
          {logs.map((log) => (
            <tr key={log.id}>
              <td>{log.id}</td>
              <td>
                <span
                  className="status-indicator"
                  style={{ backgroundColor: getStatusColor(log.result) }}
                />
                <span className="status-text">{log.result}</span>
              </td>
              <td>{log.date}</td>
              <td>{log.time}</td>
              <td>{log.finish_date}</td>
              <td>{log.finish_time}</td>
              <td>{log.user}</td>
              <td>{log.tenant.substring(0, 30)}</td>
              <td>{log.service}</td>
              <td>{log.action}</td>
              <td>{log.origin_tenant}</td>
              <td>{log.origin}</td>
              <td>{log.destination_tenant}</td>
              <td>{log.destination}</td>
              <td>{log.log_result}</td>
            </tr>
          ))}
        </tbody>
      </table>
      <div className="pagination">
        <button className="log-button" 
          onClick={handlePreviousPage} 
          disabled={currentPage === 1} 
        >
          Anterior
        </button>
        <span>Pág. {currentPage} de {totalPages}</span>
        <button className="log-button" onClick={handleNextPage} disabled={currentPage === totalPages} 
        >
          Próximo
        </button>
      </div>
    </div>
  );
};

export default Log;
