import React, { useState, useEffect, useRef } from "react";
import socket, { Message } from "../.././socket";

const Forum: React.FC = () => {
    const [messages, setMessages] = useState<Message[]>([]);
    const [newMessage, setNewMessage] = useState<string>("");
    const username: string = localStorage.getItem("username") || "";
    const messagesContainerRef = useRef<HTMLDivElement | null>(null); // Reference for the message container

    // Function to scroll to the bottom of the messages container
    const scrollToBottom = () => {
        if (messagesContainerRef.current) {
            messagesContainerRef.current.scrollTop = messagesContainerRef.current.scrollHeight;
        }
    };

    useEffect(() => {
        // Fetch messages from the get-forum endpoint
        fetch("/api/get-forum")
            .then((response) => response.json())
            .then((data) => {
                setMessages(data);
                setTimeout(scrollToBottom, 100); // Delay to ensure DOM rendering
            })
            .catch((error) => {
                console.error("Error fetching forum messages:", error);
            });

        // Set up Socket.IO listeners
        socket.on("connect", () => {
            console.log("Connected to the server via Socket.IO");
        });

        // Listen for incoming messages from the server
        socket.on("forum", (message: Message) => {
            setMessages((prevMessages) => [...prevMessages, message]);
            setTimeout(scrollToBottom, 100); // Delay to ensure DOM updates
        });

        // socket.on("connect_error", (error) => {
        //     console.error("Connection error:", error);
        // });

        // Clean up listeners on unmount
        return () => {
            socket.off("connect");
            socket.off("forum");
            socket.off("connect_error");
        };
    }, []);

    const handleSendMessage = () => {
        if (newMessage.trim() !== "") {
            const now = new Date();
            const message = {
                channel: "forum",
                user_id: username,
                user_name: username,
                date: now.toLocaleDateString("pt-BR"),
                time: now.toLocaleTimeString([], { hour: '2-digit', minute: '2-digit', hour12: false }),
                content: newMessage,
                destination: "all",
            };

            // Send the message to the server
            socket.emit("new_message", message);
            setNewMessage(""); // Clear the input field

            setTimeout(scrollToBottom, 100); // Delay to ensure DOM rendering
        }
    };

    return (
        <div>
            <div
                ref={messagesContainerRef} // Attach ref to the container
                style={{ border: "1px solid #ccc", height: "500px", overflowY: "scroll" }}
            >
                <table style={{ width: "100%", borderCollapse: "collapse" }}>
                    <thead style={{ position: "sticky", top: "0", backgroundColor: "#f9f9f9", zIndex: 1 }}>
                        <tr style={{ borderBottom: "1px solid #ccc", textAlign: "left" }}>
                            <th style={{ padding: "10px" }}>Usuário</th>
                            <th style={{ padding: "10px", width: "70%" }}>Mensagem</th>
                            <th style={{ padding: "10px" }}>Data/Hora</th>
                        </tr>
                    </thead>
                    <tbody>
                        {messages.map((dados, index) => (
                            <tr key={`${dados.data.id}-${index}`} style={{ borderBottom: "1px solid #eee" }}>
                                <td style={{ padding: "10px" }}>{dados.data.user_id}</td>
                                <td style={{ padding: "10px" }} dangerouslySetInnerHTML={{ __html: dados.data.content }}></td>
                                <td style={{ padding: "10px" }}>
                                <small>{dados.data.date} - {dados.data.time}</small>
                                </td>
                            </tr>
                        ))}
                    </tbody>
                </table>
            </div>
            <div style={{ marginTop: "10px" }}>
                <input
                    type="text"
                    value={newMessage}
                    onChange={(e) => setNewMessage(e.target.value)}
                    placeholder="Digite sua mensagem"
                    style={{ width: "80%", padding: "10px", marginRight: "10px" }}
                />
                <button className="Config-btn-new" onClick={handleSendMessage} style={{ padding: "10px 20px" }}>
                    Enviar
                </button>
            </div>
        </div>
    );
};

export default Forum;
